import { AfterViewInit, Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { TranslationLoaderService } from '@app/modules/shared-module/services/i18n/translation-loader.service';
import { ValidationLoader } from '@app/modules/shared-module/services/i18n/validations.loader';
import { ViewportScroller } from '@angular/common';
import { Router, ActivatedRoute } from '@angular/router';
import {InterComponentCommunicationService} from '@app/modules/simulation/service/inter-component-communication.service';

@Component({
  selector: 'app-simulation-sent',
  templateUrl: './simulation-sent.component.html',
  styleUrls: ['./simulation-sent.component.scss']
})
export class SimulationSentComponent implements OnInit, AfterViewInit {
  viewportScrollAnchor: string;

  translations: any;
  showChecklistBtn = false;
  checklistAnchor: string;

  constructor(
    private translate: TranslateService,
    private translationLoader: TranslationLoaderService,
    private validationsLoader: ValidationLoader,
    private viewportScroller: ViewportScroller,
    private router: Router,
    private route: ActivatedRoute,
    private interComponentCommunicationService: InterComponentCommunicationService
  ) {
    this.translate.use('pl');
    this.translationLoader.loadTranslations();
    this.validationsLoader.init();

    this.translate.get('SimulatorResults').subscribe(
      translated => {
        this.translations = translated;
      });
  }

  ngOnInit() {
    this.viewportScrollAnchor = this.interComponentCommunicationService.viewportScrollAnchor;
    this.showChecklistBtn = this.interComponentCommunicationService.isChecklistBtnVisible;
    this.checklistAnchor = this.interComponentCommunicationService.checklistAnchor;
  }

  ngAfterViewInit() {
    this.returnToTopPagePosition();
  }

  private returnToTopPagePosition() {
    this.viewportScroller.scrollToAnchor(this.viewportScrollAnchor);
  }

  goToChecklistWebComponent() {
    this.viewportScroller.scrollToAnchor(this.checklistAnchor);
    this.interComponentCommunicationService.setUpSimulationStep(0);
    this.router.navigate(['../'], { relativeTo: this.route, skipLocationChange: true });
  }

  startNewSimulation() {
    this.interComponentCommunicationService.setUpSimulationStep(0);
    this.router.navigate(['../'], { relativeTo: this.route, skipLocationChange: true });
  }
}
