import { Component, OnDestroy } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { TranslationLoaderService } from '@app/modules/shared-module/services/i18n/translation-loader.service';
import { InterComponentCommunicationService } from '../../service/inter-component-communication.service';

@Component({
  selector: 'app-simulation-home',
  templateUrl: './simulation-home.component.html',
  styleUrls: ['./simulation-home.component.scss']
})
export class SimulationHomeComponent implements OnDestroy {
  viewportScrollAnchor: string;

  constructor(
    private translate: TranslateService,
    private translationLoader: TranslationLoaderService,
    private interComponentCommunicationService: InterComponentCommunicationService
  ) {
    this.translate.use('pl');
    this.translationLoader.loadTranslations();
  }

  ngOnDestroy() {
    this.interComponentCommunicationService.setShouldScrollToTopPageOnHomePage(true);
  }
}
