import { Component } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { timer } from 'rxjs/internal/observable/timer';
import { environment as env } from '@env/environment';


@Component({
  selector: 'app-loader',
  template: '<div *ngIf="isVisible()" class="app-loader"></div>',
  styleUrls: ['./loader.component.scss']
})
export class LoaderComponent {


  private static visible = false;
  public static show: () => Observable<number> = () => {
    LoaderComponent.visible = true;
    return LoaderComponent.createTimer();
  }

  public static hide: () => void = () => LoaderComponent.visible = false;

  private static createTimer(): Observable<number> {
    return timer(env.methodTimeoutInMillis);
  }

  public isVisible: () => boolean = () => LoaderComponent.visible;


}


