import { NgModule } from '@angular/core';
import { CommonModule, DecimalPipe } from '@angular/common';

import { HttpClientModule } from '@angular/common/http';

import { ReactiveFormsModule } from '@angular/forms';
import { FormlyBootstrapModule } from '@ngx-formly/bootstrap';
import { FormlyModule } from '@ngx-formly/core';

import { TranslateService, TranslateModule } from '@ngx-translate/core';
import { TranslationLoaderService } from './services/i18n/translation-loader.service';
import { ValidationLoader } from './services/i18n/validations.loader';


import {
  ButtonsModule,
  AccordionModule,
  TooltipModule,
  AlertModule,
  ModalModule
} from 'ngx-bootstrap';

import {
  HorizontalWrapperComponent,
  VerticalWrapperComponent,
  SectionInfoComponent,
} from './wrappers';
import { ViewDataContainerComponent, SimulationResultsViewComponent } from './components';
import {
  RepeatSectionComponent,
  CustomRadioComponent,
  CustomInputComponent,
  DigitInputComponent,
  CheckboxWithTooltipComponent,
  CheckboxWithTooltipOnClickComponent
} from './types';

import { DigitOnlyDirective } from './directives/digit-only.directive';
import { SpacePipe, InstalmentSufix } from './pipes';
import { LoaderComponent } from '@app/modules/shared-module/components/loader/loader.component';
import { TogglerComponent } from '@app/modules/shared-module/components/toggler/toggler.component';
import {UserAuthService} from '@app/modules/shared-module/services/user-auth/user-auth.service';

@NgModule({
  declarations: [
    HorizontalWrapperComponent,
    VerticalWrapperComponent,
    ViewDataContainerComponent,
    SimulationResultsViewComponent,
    RepeatSectionComponent,
    SectionInfoComponent,
    CustomRadioComponent,
    TogglerComponent,
    CustomInputComponent,
    DigitInputComponent,
    CheckboxWithTooltipComponent,
    CheckboxWithTooltipOnClickComponent,
    DigitOnlyDirective,
    SpacePipe,
    InstalmentSufix,
    LoaderComponent
  ],
  imports: [
    CommonModule,
    HttpClientModule,
    TranslateModule.forRoot(),
    ReactiveFormsModule,
    FormlyBootstrapModule,
    FormlyModule.forRoot({
      wrappers: [
        { name: 'form-field-horizontal', component: HorizontalWrapperComponent },
        { name: 'form-field-vertical', component: VerticalWrapperComponent },
        { name: 'section-info', component: SectionInfoComponent }
      ],
      types: [
        { name: 'repeat-section', component: RepeatSectionComponent },
        { name: 'custom-radio', component: CustomRadioComponent },
        { name: 'custom-input', component: CustomInputComponent },
        { name: 'digit-input', component: DigitInputComponent },
        { name: 'checkbox-with-tooltip', component: CheckboxWithTooltipComponent },
        { name: 'checkbox-with-tooltip-onclick', component: CheckboxWithTooltipOnClickComponent }
      ]
    }),
    ButtonsModule.forRoot(),
    AccordionModule.forRoot(),
    TooltipModule.forRoot(),
    AlertModule.forRoot(),
    ModalModule.forRoot(),
  ],
  exports: [
    TranslateModule,
    ReactiveFormsModule,
    FormlyBootstrapModule,
    FormlyModule,
    ButtonsModule,
    AccordionModule,
    TooltipModule,
    AlertModule,
    ModalModule,
    ViewDataContainerComponent,
    SimulationResultsViewComponent,
    LoaderComponent,
    TogglerComponent
  ],
  providers: [
    TranslateService,
    TranslationLoaderService,
    ValidationLoader,
    DecimalPipe,
    UserAuthService
  ],
})
export class SharedModule {
}
