import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {environment as env} from '../../../../environments/environment';
import {
  CheckListData,
} from '@app/modules/checklist/model/CheckListData';
import {Observable} from 'rxjs/internal/Observable';
import {ChecklistGenerationResultDto} from '@app/modules/checklist/dto/ChecklistGenerationResultDto';
import {ChecklistResultsWithEmailBodyDto} from '@app/modules/checklist/dto/ChecklistResultsWithEmailBodyDto';
import {ChecklistEnvelope} from '@app/modules/checklist/dto/ChecklistEnvelopeDto';

@Injectable({
  providedIn: 'root'
})
export class ChecklistControlService {

  constructor(private httpClient: HttpClient) {}

  private prepareHeaders(): HttpHeaders {
    return new HttpHeaders().append('Content-Type', 'application/json');
  }

  makeChecklistRequest$(request: CheckListData): Observable<ChecklistGenerationResultDto> {
    const headers = this.prepareHeaders();

    if (env.checklist.isMock) {
      return this.httpClient.get<ChecklistGenerationResultDto>(env.checklist.url);
    } else {
      return this.httpClient.post<ChecklistGenerationResultDto>(env.checklist.url, request, {headers});
    }
  }

  sendMail$(checklists: ChecklistResultsWithEmailBodyDto): Observable<void> {
    const headers = this.prepareHeaders();

    if (env.checklist.isMock) {
      return this.httpClient.get<void>(env.checklist.emailUrl);
    } else {
      return this.httpClient.post<void>(env.checklist.emailUrl, checklists, {headers});
    }
  }

  saveChecklist$(checklist: ChecklistEnvelope) {
    const headers = this.prepareHeaders();

    if (env.checklist.isMock) {
      return this.httpClient.get<void>(env.checklist.saveUrl);
    } else {
      return this.httpClient.post<void>(env.checklist.saveUrl, checklist, {headers});
    }
  }
}
