import { FormlyFieldConfig, FormlyConfig } from '@ngx-formly/core';
import { TranslateService } from '@ngx-translate/core';
import { Injectable } from '@angular/core';
import { FormControl, ValidationErrors } from '@angular/forms';
import { SpacePipe } from '../../pipes';

@Injectable()
export class ValidationLoader {
    constructor(
        private translate: TranslateService,
        private formlyConfig: FormlyConfig,
        private spacePipe: SpacePipe
        ) { }

    private static phoneAllowed(testedValue) {
      return /^\d{9}$/i.test(testedValue);
    }

    private static phoneNotAllowed(testedValue) {
      const negativeRegexps = [
        /^0.*$/i,
        /^700.*$/i,
        /^800.*$/i,
        /^900.*$/i,
        /^123456789$/i,
        /^0{9}$/i,
        /^1{9}$/i,
        /^2{9}$/i,
        /^3{9}$/i,
        /^4{9}$/i,
        /^5{9}$/i,
        /^6{9}$/i,
        /^7{9}$/i,
        /^8{9}$/i,
        /^9{9}$/i];

      for (let i = 0; i < negativeRegexps.length; i++) {
        if (negativeRegexps[i].test(testedValue)) {
          return true;
        }
      }
      return false;
    }

    init(): void {
        // custom validations
        this.formlyConfig.setValidator({name: 'email', validation: this.emailValidator});
        this.formlyConfig.setValidator({name: 'phone', validation: this.phoneValidator});
        this.formlyConfig.setValidator({name: 'firstName', validation: this.firstNameValidator});
        this.formlyConfig.setValidator({name: 'lastName', validation: this.lastNameValidator});

        // message without params
        this.formlyConfig.addValidatorMessage('required', (err, field) => this.translate.instant('Validations.required'));
        this.formlyConfig.addValidatorMessage('email', (err, field) => this.translate.instant('Validations.email'));
        this.formlyConfig.addValidatorMessage('phone', (err, field) => this.translate.instant('Validations.phone'));
        this.formlyConfig.addValidatorMessage('firstName', (err, field) => this.translate.instant('Validations.firstName'));
        this.formlyConfig.addValidatorMessage('lastName', (err, field) => this.translate.instant('Validations.lastName'));

        // message with params
        this.formlyConfig.addValidatorMessage('minlength', (err, field) => this.minlengthValidationMessage(err, field, this.translate));
        this.formlyConfig.addValidatorMessage('maxlength', (err, field) => this.maxlengthValidationMessage(err, field, this.translate));
        this.formlyConfig.addValidatorMessage('min', (err, field) => this.minValidationMessage(err, field, this.translate));
        this.formlyConfig.addValidatorMessage('max', (err, field) => this.maxValidationMessage(err, field, this.translate));
    }

    private minlengthValidationMessage(err, field: FormlyFieldConfig, translate: TranslateService) {
        return translate.instant('Validations.minLength', { number: field.templateOptions.minLength});
    }

    private maxlengthValidationMessage(err, field: FormlyFieldConfig, translate: TranslateService) {
        return translate.instant('Validations.maxLength', { number: field.templateOptions.maxLength});
    }

    private minValidationMessage(err, field: FormlyFieldConfig, translate: TranslateService) {
        return translate.instant('Validations.min', { number: this.spacePipe.transform(field.templateOptions.min)});
    }

    private maxValidationMessage(err, field: FormlyFieldConfig, translate: TranslateService) {
        return translate.instant('Validations.max', { number: this.spacePipe.transform(field.templateOptions.max)});
    }

    private emailValidator(control: FormControl): ValidationErrors {
      return /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(control.value) ? null : { email: true };
    }

    private firstNameValidator(control: FormControl): ValidationErrors {
      return /^([a-zA-ZżźćńółęąśŻŹĆĄŚĘŁÓŃ]+\s)*[a-zA-ZżźćńółęąśŻŹĆĄŚĘŁÓŃ]+$/i.test(control.value) ? null : { firstName: true };
    }

    private lastNameValidator(control: FormControl): ValidationErrors {
      return /^([a-zA-ZżźćńółęąśŻŹĆĄŚĘŁÓŃ,.'-]+\s)*[a-zA-ZżźćńółęąśŻŹĆĄŚĘŁÓŃ,.'-]+$/i.test(control.value) ? null : { lastName: true };
    }

    private phoneValidator(control: FormControl): ValidationErrors {
      return (ValidationLoader.phoneAllowed(control.value) && !ValidationLoader.phoneNotAllowed(control.value)) ? null : { phone: true };
    }
}


