import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {NoContentComponent} from './components';
import {SimulationBaseComponent} from '@app/modules/simulation/components/simulation-base/simulation-base.component';
import {ChecklistBaseComponent} from '@app/modules/checklist/components/checklist-base/checklist-base.component';
import {ContactComponent} from '@app/modules/contact/components/contact/contact.component';
import {ContactConfirmationComponent} from '@app/modules/contact/components/contact-confirmation/contact-confirmation.component';

const routes: Routes = [
  {path: '**', component: NoContentComponent},
  {path: 'simulation-base', component: SimulationBaseComponent},
  {path: 'checklist-base', component: ChecklistBaseComponent},
  {path: 'kontakt', component: ContactComponent, outlet: 'contact'},
  {path: 'kontakt/dziekujemy', component: ContactConfirmationComponent, outlet: 'contact'},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
