import { Inject, Injectable } from '@angular/core';
import { SimulationResult } from '@app/modules/simulation/model/SimulationResult';
import {
  EmailParams,
  SimpleSimulationRequestWithInput, SimpleSimulationWithAdditionalParameters,
  SimulationRequestDto, SimulationResponse,
  SimulationResultsWithEmailBody,
  SimulationResultWithInput, SimulationToSentByMailRequestDto
} from '../dto/SimulationRequestDto';
import { Observable, BehaviorSubject } from 'rxjs';
import { SimulationControlService } from '@app/modules/simulation/service/simulation-control.service';
import { environment as env } from '@env/environment';
import { LOCAL_STORAGE, StorageService } from 'ngx-webstorage-service';
import { ContactService } from '@app/modules/contact/service/contact.service';
import { TranslateService } from '@ngx-translate/core';
import { TranslationLoaderService } from '@app/modules/shared-module/services/i18n/translation-loader.service';
import { LeadData } from '@app/modules/contact/model/contact';

@Injectable({
  providedIn: 'root'
})
export class SimulationService {

  private simulationResult = new BehaviorSubject(new SimulationResult([]));
  currentSimulationResult = this.simulationResult.asObservable();

  // simulationParameters: SimulationRequestDto;

  private simulationParameters = new BehaviorSubject({});
  currentSimulationParameters = this.simulationParameters.asObservable();

  translations: any;

  simulationResultsToSend;
  simulationParametersToShare;

  constructor(
    private simulationControlService: SimulationControlService,
    @Inject(LOCAL_STORAGE) private storage: StorageService,
    private contactService: ContactService,
    private translate: TranslateService,
    private translationLoader: TranslationLoaderService
  ) {
    this.translate.use('pl');
    this.translationLoader.loadTranslations();
    this.currentSimulationResult.subscribe(data => this.simulationResultsToSend = data);
    this.currentSimulationParameters.subscribe(data => this.simulationParametersToShare = data);
  }

  recaluculate(simulationParameters: any): Observable<SimulationResult> {
    this.simulationParameters.next(simulationParameters);

    if (simulationParameters.installmentMonthlyAmount) {
      return this.simulationControlService.getSimulationResultsByInstallment$(this.simulationParametersToShare);
    } else {
      return this.simulationControlService.getSimulationResultsByLoan$(this.simulationParametersToShare);
    }
  }
  getSimulationResults$(simulationInput: any): Observable<SimulationResult> {
    const simulationParameters = this.mapSimulationInputToDataToSend(simulationInput);
    this.simulationParameters.next(simulationParameters);

    if (this.simulationParametersToShare.installmentMonthlyAmount) {
      return this.simulationControlService.getSimulationResultsByInstallment$(this.simulationParametersToShare);
    } else {
      return this.simulationControlService.getSimulationResultsByLoan$(this.simulationParametersToShare);
    }
  }

  saveResults(currentSimulationResult: SimulationResult) {
    this.simulationResult.next(currentSimulationResult);
  }

  saveSimulationResult$(leadId: string): Observable<any> {
    if (this.simulationParametersToShare.installmentMonthlyAmount) {
      const resultWithInput: SimpleSimulationRequestWithInput = {
        simulations: this.simulationResultsToSend,
        simpleSimulationWithInstallmentMonthlyAmount: this.simulationParametersToShare,
        leadId
      };
      return this.simulationControlService.saveSimulation(resultWithInput);
    } else {
      const resultWithInput: SimpleSimulationRequestWithInput = {
        simulations: this.simulationResultsToSend,
        simpleSimulationWithTotalLoanAmount: this.simulationParametersToShare,
        leadId
      };
      return this.simulationControlService.saveSimulation(resultWithInput);
    }
  }

  mapSimulationInputToDataToSend(simulationInput: any): SimulationRequestDto {
    const simpleSimulationRequestToSend = {
      loanTermInYears: simulationInput.loanTermInYears,
      familySize: simulationInput.familySize,
      totalIncomeAmount: simulationInput.totalIncomeAmount,
      totalCostOfLiving: simulationInput.totalCostOfLiving,
      totalCostOfLiabilitiesLoan: simulationInput.totalCostOfLiabilitiesLoan,
      totalCostOfLiabilitiesCreditCards: simulationInput.totalCostOfLiabilitiesCreditCards,
      expectedContributionAmount: simulationInput.expectedContributionAmount,
    };
    if (simulationInput.installmentMonthlyAmount) {
      return {
        simpleSimulationRequest: simpleSimulationRequestToSend,
        installmentMonthlyAmount: simulationInput.installmentMonthlyAmount
      };
    } else {
      return {
        simpleSimulationRequest: simpleSimulationRequestToSend,
        totalLoanAmount: simulationInput.totalLoanAmount
      };
    }
  }

  sendSimulation$(recipient: string): Observable<void> {
    const interestRate: number =
      this.simulationResultsToSend.simulations[0].interestRate; // Każda symulacja ma ten sam.

    const simulationResponses: SimulationResponse[] = [];
    let translatedVariantName: string;
    this.simulationResultsToSend.simulations.forEach((simulation => {
      this.translate.get('SimulatorResults.switch').subscribe(translated => {
        translatedVariantName = translated[simulation.simulationVariant.toLocaleLowerCase()];
      });
      simulationResponses.push({
        simulationName: translatedVariantName,
        simulationVariant: simulation.simulationVariant,
        installmentMonthlyAmount: simulation.installmentMonthlyAmount,
        propertyValue: simulation.propertyValue,
        loanAmount: simulation.loanAmount,
        numberOfInstallmentsInYears: simulation.numberOfInstallmentsInYears,
        maxLoanCapability: simulation.maxLoanCapability,
      });
    }));

    const simulationsToSend: SimulationResultsWithEmailBody =
      this.createSimulationResultsWithEmailBody(interestRate, simulationResponses, recipient);

    if (this.simulationParametersToShare.installmentMonthlyAmount) {
      return this.simulationControlService.sendMailByInstallment$(simulationsToSend);
    } else {
      return this.simulationControlService.sendMailByByLoan$(simulationsToSend);
    }
  }

  saveLead(leadData: LeadData): Observable < any > {
    return this.contactService.saveLead(leadData);
  }

  private createSendSimpleSimulationByMailRequest(simpleSimulationWithAdditionalParameters:
                                                      SimpleSimulationWithAdditionalParameters): SimulationToSentByMailRequestDto {
    if (this.simulationParametersToShare.installmentMonthlyAmount) {
      return {
        simpleSimulationWithAdditionalParameters,
        installmentMonthlyAmount: this.simulationParametersToShare.installmentMonthlyAmount
      };
    } else {
      return {
        simpleSimulationWithAdditionalParameters,
        totalLoanAmount: this.simulationParametersToShare.totalLoanAmount
      };
    }
  }

  private createSimulationResultsWithEmailBody(interestRate,
                                               simulationResponses: SimulationResponse[],
                                               recipient): SimulationResultsWithEmailBody {
    const simpleSimulationWithAdditionalParameters: SimpleSimulationWithAdditionalParameters = {
      simpleSimulationRequest: this.simulationParametersToShare.simpleSimulationRequest,
      interestRate
    };

    const simpleSimulationToSentByMailRequest: SimulationToSentByMailRequestDto =
      this.createSendSimpleSimulationByMailRequest(simpleSimulationWithAdditionalParameters);

    const result: SimulationResultWithInput = {
      simulationResponses,
      simulationRequest: simpleSimulationToSentByMailRequest
    };

    const emailParams: EmailParams = {
      simulationsLength: simulationResponses.length
    };

    return {
      body: emailParams,
      simulations: result,
      recipient
    };
  }
}
