import { Injectable } from '@angular/core';
import {CheckListData} from '@app/modules/checklist/model/CheckListData';

@Injectable({
  providedIn: 'root'
})
export class InterComponentCommunicationService {
  isSimulationBtnVisible = false;
  simulationAnchor: string;
  checklistLastActiveStep = 0;
  isChecklistResultView = false;

  viewportScrollAnchor: string;
  checkListData: CheckListData;
  shouldScrollToTopPageOnHomePage = false;

  showSimulationBtn(simulationAnchor: string) {
    this.isSimulationBtnVisible = true;
    this.simulationAnchor = simulationAnchor;
  }

  setViewportScrollAnchor(viewportScrollAnchor: string) {
    this.viewportScrollAnchor = viewportScrollAnchor;
  }

  setUpSimulationStep(stepNumber: number) {
    this.checklistLastActiveStep = stepNumber;
  }

  setChecklistData(checkListData: CheckListData) {
    this.checkListData = checkListData;
  }

  setIsChecklistResultView(isChecklistResultView: boolean) {
    this.isChecklistResultView = isChecklistResultView;
  }

  setShouldScrollToTopPageOnHomePage(shouldScrollToTopPageOnHomePage: boolean) {
    this.shouldScrollToTopPageOnHomePage = shouldScrollToTopPageOnHomePage;
  }
}
