import {Injectable} from '@angular/core';
import {Observable} from 'rxjs';
import {ChecklistControlService} from '@app/modules/checklist/service/checklist-control.service';
import {CheckListRequestDto} from '@app/modules/checklist/dto/ChecklistRequestDto';
import {ChecklistGenerationResultDto} from '@app/modules/checklist/dto/ChecklistGenerationResultDto';
import {
  ChecklistDocument,
  ChecklistDocuments,
  ChecklistResult,
  ChecklistResultsWithEmailBodyDto
} from '@app/modules/checklist/dto/ChecklistResultsWithEmailBodyDto';
import {LeadData} from '@app/modules/contact/model/contact';
import {ContactService} from '@app/modules/contact/service/contact.service';
import {ChecklistEnvelope} from '@app/modules/checklist/dto/ChecklistEnvelopeDto';

@Injectable({
  providedIn: 'root'
})
export class ChecklistService {
  currentChecklistResult: ChecklistGenerationResultDto;

  constructor(
    private checklistControlService: ChecklistControlService,
    private contactService: ContactService
  ) { }

  fetchDocuments(request: CheckListRequestDto): Observable<ChecklistGenerationResultDto> {
    return this.checklistControlService.makeChecklistRequest$(request);
  }

  updateChecklistResult(result: ChecklistGenerationResultDto) {
    this.currentChecklistResult = result;
  }

  getCheckListResults(): ChecklistGenerationResultDto {
    return this.deepCopy(this.currentChecklistResult);
  }

  saveChecklist(leadId: string) {
    const checklistEnvelope: ChecklistEnvelope = {
      leadId,
      documents: this.currentChecklistResult.documents
    };
    return this.checklistControlService.saveChecklist$(checklistEnvelope);
  }

  sendChecklist(recipient: string): Observable<void> {
    return this.checklistControlService.sendMail$(this.prepareChecklistDataToSend(recipient));
  }

  private prepareChecklistDataToSend(recipient: string): ChecklistResultsWithEmailBodyDto {
    const applicants: ChecklistDocuments[] = [];
    const creditPurposes: ChecklistDocuments[] = [];
    this.currentChecklistResult.documents.sections.forEach(section => {
      section.entries.forEach(sectionEntry => {
        const documents: ChecklistDocument[] = [];
        sectionEntry.entries.forEach(entry => {
          documents.push({
              order: entry.order,
              name: entry.name,
              url: entry.url
            }
          );
        });
        if (section.code === 'requesters') {
          applicants.push({documents});
        }
        if (section.code === 'requestPurposes') {
          creditPurposes.push({documents});
        }
      });
    });

    const checklistResult: ChecklistResult = {
      applicants,
      creditPurposes
    };
    return {
      checklist: checklistResult,
      recipient
    };
  }

  saveLead(leadData: LeadData): Observable<any> {
    return this.contactService.saveLead(leadData);
  }

  deepCopy<T>(target: T): T {
    if (target === null) {
      return target;
    }
    if (target instanceof Date) {
      return new Date(target.getTime()) as any;
    }
    if (target instanceof Array) {
      const cp = [] as any[];
      (target as any[]).forEach((v) => { cp.push(v); });
      return cp.map((n: any) => this.deepCopy<any>(n)) as any;
    }
    if (typeof target === 'object' && target !== {}) {
      const cp = { ...(target as { [key: string]: any }) } as { [key: string]: any };
      Object.keys(cp).forEach(k => {
        cp[k] = this.deepCopy<any>(cp[k]);
      });
      return cp as T;
    }
    return target;
  }
}
