import {Inject, Injector, NgModule} from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { SharedModule } from '@app/modules/shared-module/shared.module';

import { AppRoutingModule } from '@app/app-routing.module';
import { AppComponent } from '@app/app.component';

import { NoContentComponent } from '@components/index';

import { createCustomElement } from '@angular/elements';

import { SimulationModule } from '@app/modules/simulation/simulation.module';
import { SimulationBaseComponent } from '@app/modules/simulation/components/simulation-base/simulation-base.component';
import { ChecklistModule } from '@app/modules/checklist/checklist.module';
import { ChecklistBaseComponent } from '@app/modules/checklist/components/checklist-base/checklist-base.component';
import { ContactModule } from '@app/modules/contact/contact.module';
import { ContactBaseComponent } from './modules/contact/components';
import { LoginHomeComponent } from '@app/modules/login/login-home/login-home.component';
import { LoginModule } from '@app/modules/login/login.module';
import { environment as env} from '../environments/environment';
import { StorageService, LOCAL_STORAGE } from 'ngx-webstorage-service';
import { UserAuthService } from '@app/modules/shared-module/services/user-auth/user-auth.service';
import { UserContext } from '@app/modules/shared-module/model';


@NgModule({
  declarations: [
    AppComponent,
    NoContentComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    SharedModule,
    ChecklistModule,
    SimulationModule,
    ContactModule,
    LoginModule,
    AppRoutingModule
  ],
  providers: [],
  bootstrap: [],
  entryComponents: [ContactBaseComponent, SimulationBaseComponent, ChecklistBaseComponent]
})
export class AppModule {

  constructor(
    private injector: Injector,
    @Inject(LOCAL_STORAGE) private storage: StorageService,
    private userService: UserAuthService
  ) {
    const simulationModule = createCustomElement(SimulationBaseComponent, { injector });
    const contactModule = createCustomElement(ContactBaseComponent, { injector });
    const checklistModule = createCustomElement(ChecklistBaseComponent, { injector });
    const loginModule = createCustomElement(LoginHomeComponent, { injector });
    customElements.define('simulation-module', simulationModule);
    customElements.define('contact-module', contactModule);
    customElements.define('checklist-module', checklistModule);
    customElements.define('login-module', loginModule);
  }

  ngDoBootstrap() { }

}

