export class CheckListData {
  constructor(
    public loanAmount: number = null,
    public creditPurposes: CreditPurpose[] = [new CreditPurpose()],
    public applicants: Applicant[] = [new Applicant()],
    public email: string = ''
  ) {
  }
}

class CreditPurpose {
  constructor(
    public creditPurpose: string = '',
    public flatPurchaseType?: string,
    public housePurchaseType?: string,
    public propertyState?: string,
    public refinancingPurpose?: string,
    public refinancingType?: string,
  ) {
  }
}

class Applicant {
  constructor(
    public maritalStatus: string = '',
    public regimeOfSeparateProperty?: string,
    public incomes: ApplicantIncome[] = [new ApplicantIncome()]
  ) {
  }
}

export class ApplicantIncome {
  constructor(
    public incomeType: string = '',
    public employmentContractType?: string,
    public employmentIncomeTypeBankAccount?: boolean,
    public employmentIncomeTypePremium?: boolean,
    public employmentIncomeTypeInternationalIncome?: boolean,
    public b2bContractType?: string,
    public b2bContractBillingType?: string,
    public farmIncomeTypeSpecialBranches?: boolean,
    public farmIncomeTypeRecordKeeping?: boolean,
    public farmIncomeTypeTenancy?: boolean,
    public otherIncomeTypePension?: boolean,
    public otherIncomeTypeAnnuity?: boolean,
    public otherIncomeTypeSeafarer?: boolean,
    public otherIncomeTypeRentalAgreement?: boolean,
    public otherIncomeType500plus?: boolean,
    public otherIncomeTypeSupervisoryBoardMember?: boolean,
  ) {}
}

export class ChecklistGenerationRequest {
}

interface ChecklistEntry {
  order: number;
  documentId: string;
  name: string;
  shortName: string;
  url: string;
}

export interface ChecklistSubSection {
  code: string;
  entries: ChecklistEntry[];
}

export interface ChecklistSection {
  code: string;
  entries: ChecklistSubSection[];
}

export interface ChecklistGenerationResult {
  documents: {
    sections: ChecklistSection[];
  };
}

export interface ChecklistResult {
  documentsListText: string;
  documentsListUrl: string;
}

export interface ChecklistResultsWithEmailBody {
  body: EmailBody;
  checklists: ChecklistResult[];
  recipient: string;
}

export interface EmailBody {
  contactFormText: string;
  contactFormUrl: string;
  documentsListText: string;
  documentsListUrl: string;
}




