import { AfterViewInit, Component, Inject, OnInit } from '@angular/core';
import { FormlyFieldConfig } from '@ngx-formly/core';
import { FormControl, FormGroup } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { TranslationLoaderService } from '@app/modules/shared-module/services/i18n/translation-loader.service';
import { ValidationLoader } from '@app/modules/shared-module/services/i18n/validations.loader';
import { LoaderComponent } from '@app/modules/shared-module/components/loader/loader.component';
import { EMPTY } from 'rxjs';
import { ChecklistService } from '@app/modules/checklist/service/checklist.service';
import { ActivatedRoute, Router } from '@angular/router';
import { catchError, flatMap, map, take } from 'rxjs/operators';
import { ViewportScroller } from '@angular/common';
import { SESSION_STORAGE, StorageService } from 'ngx-webstorage-service';
import { InterComponentCommunicationService } from '@app/modules/checklist/service/inter-component-communication.service';
import { UserAuthService } from '@app/modules/shared-module/services/user-auth/user-auth.service';
import {environment as env} from '@env/environment';


@Component({
  selector: 'app-checklist-send',
  templateUrl: './checklist-send.component.html',
  styleUrls: ['./checklist-send.component.scss']
})
export class ChecklistSendComponent implements AfterViewInit, OnInit {
  translations: any;
  methodUnavailable = false;
  fields: FormlyFieldConfig[];
  form = new FormGroup({});
  model: any = {};
  private viewportScrollAnchor: string;
  rodoInformationURL: string;

  constructor(
    private translate: TranslateService,
    private translationLoader: TranslationLoaderService,
    private validationsLoader: ValidationLoader,
    private checklistService: ChecklistService,
    private router: Router,
    private route: ActivatedRoute,
    private viewportScroller: ViewportScroller,
    private interComponentCommunicationService: InterComponentCommunicationService,
    @Inject(SESSION_STORAGE) private storage: StorageService,
    public userService: UserAuthService
  ) {
    this.translate.use('pl');
    this.translationLoader.loadTranslations();
    this.validationsLoader.init();

    this.translate.get('SimulatorResults').subscribe(translated => {
      this.translations = translated;
    });

    this.fields = this.getFields();
    this.rodoInformationURL = env.rodoInformationURL;
  }

  ngOnInit() {
    this.viewportScrollAnchor = this.interComponentCommunicationService.viewportScrollAnchor;
  }

  ngAfterViewInit() {
    this.returnToTopChecklistPosition();
  }

  getFields(): FormlyFieldConfig[] {
    const translate: TranslateService = this.translate;
    return [
      {
        fieldGroup: [
          {
            className: 'col-12 app-justify-content-center app-form-field',
            type: 'custom-input',
            wrappers: ['form-field-horizontal'],
            key: 'firstName',
            templateOptions: {
              type: 'text',
              placeholder: translate.instant('ContactForm.fields.firstName.label'),
              attributes: {},
              required: true,
            },
            validators: {
              validation: ['firstName']
            },
            hideExpression: this.userService.isBroker()
          },
          {
            className: 'col-12 app-justify-content-center app-form-field',
            type: 'custom-input',
            wrappers: ['form-field-horizontal'],
            key: 'lastName',
            templateOptions: {
              type: 'text',
              placeholder: translate.instant('ContactForm.fields.lastName.label'),
              attributes: {},
              required: true
            },
            validators: {
              validation: ['lastName']
            },
            hideExpression: this.userService.isBroker()
          },
          {
            className: 'col-12 app-justify-content-center app-form-field',
            type: 'custom-input',
            wrappers: ['form-field-horizontal'],
            key: 'email',
            templateOptions: {
              type: 'email',
              placeholder: translate.instant('ContactForm.fields.email.label'),
              attributes: {},
              required: true,
            },
            validators: {
              validation: ['email'],
            },
          },
          {
            className: 'col-12 app-justify-content-center app-form-field',
            type: 'custom-input',
            wrappers: ['form-field-horizontal'],
            key: 'phoneNumber',
            templateOptions: {
              type: 'tel',
              placeholder: translate.instant('ContactForm.fields.phoneNumber.label'),
              attributes: {},
              required: true
            },
            validators: {
              validation: ['phone'],
            },
            hideExpression: this.userService.isBroker()
          }
        ]
      },
      {
        className: 'app-checkbox-list',
        hideExpression: this.userService.isBroker(),
        fieldGroup: [
          {
            className: 'app-checkbox',
            key: 'selectAll',
            type: 'checkbox-with-tooltip-onclick',
            defaultValue: false,
            templateOptions: {
              label: translate.instant('ContactForm.consents.selectAll'),
              description: '',
              attributes: {
                tooltip: ''
              },
            },
          },
          {
            className: 'app-checkbox',
            key: 'marketingConsent',
            type: 'checkbox-with-tooltip-onclick',
            defaultValue: false,
            templateOptions: {
              label: translate.instant('ContactForm.consents.marketingConsent.labelShort'),
              description: '',
              attributes: {
                tooltip: translate.instant('ContactForm.consents.marketingConsent.label')
              },
            },
            expressionProperties: {
              'model.marketingConsent': 'model.selectAll'
            }
          },
          {
            className: 'app-checkbox',
            key: 'digitalMarketingConsent',
            type: 'checkbox-with-tooltip-onclick',
            defaultValue: false,
            templateOptions: {
              label: translate.instant('ContactForm.consents.digitalMarketingConsent.labelShort'),
              description: '',
              attributes: {
                tooltip: translate.instant('ContactForm.consents.digitalMarketingConsent.label')
              },
            },
            expressionProperties: {
              'model.digitalMarketingConsent': 'model.selectAll'
            }
          },
          {
            className: 'app-checkbox',
            key: 'offerPresentationConsent',
            type: 'checkbox-with-tooltip-onclick',
            defaultValue: false,
            templateOptions: {
              label: translate.instant('ContactForm.consents.offerPresentationConsent.labelShort'),
              description: '',
              attributes: {
                tooltip: translate.instant('ContactForm.consents.offerPresentationConsent.label')
              },
            },
            expressionProperties: {
              'model.offerPresentationConsent': 'model.selectAll'
            }
          },
          {
            className: 'app-checkbox',
            key: 'digitalOfferPresentationConsent',
            type: 'checkbox-with-tooltip-onclick',
            defaultValue: false,
            templateOptions: {
              label: translate.instant('ContactForm.consents.digitalOfferPresentationConsent.labelShort'),
              description: '',
              attributes: {
                tooltip: translate.instant('ContactForm.consents.digitalOfferPresentationConsent.label')
              },
            },
            expressionProperties: {
              'model.digitalOfferPresentationConsent': 'model.selectAll'
            }
          }
        ]
      }
    ];
  }

  submit() {
    if (this.form.valid) {
      delete this.model.selectAll;
      if (this.userService.isBroker()) {
        this.sendEmail();
      } else {
        this.saveLeadAndCheckListAndSendEmail();
      }
      LoaderComponent.show();

    }
  }
  private saveLeadAndCheckListAndSendEmail() {
    this.checklistService.saveLead(this.model).pipe(
      take(1),
      flatMap(saveLeadResult => {
        this.storage.set('leadId', saveLeadResult.id);
        return this.checklistService.saveChecklist(saveLeadResult.id);
      }),
      flatMap(() => {
        return this.checklistService.sendChecklist(this.model.email);
      }),
      map(() => {
        LoaderComponent.hide();
        this.router.navigate(['../dziekujemy'], { relativeTo: this.route, skipLocationChange: true });
        return EMPTY;
      }),
      catchError(err => {
        console.log(err);
        LoaderComponent.hide();
        this.methodUnavailable = true;
        return err;
      })
    ).subscribe();
  }

  private sendEmail() {
    this.checklistService.sendChecklist(this.model.email).subscribe(value => {
        LoaderComponent.hide();
        this.router.navigate(['../dziekujemy'], { relativeTo: this.route, skipLocationChange: true });
      },
      err => {
        console.log(err);
        LoaderComponent.hide();
        this.methodUnavailable = true;
        return err;
      });
  }

  onAlertClosed(): void {
    this.methodUnavailable = false;
  }

  private returnToTopChecklistPosition() {
    this.viewportScroller.scrollToAnchor(this.viewportScrollAnchor);
  }

  backToChecklist() {
    this.returnToTopChecklistPosition();
    this.interComponentCommunicationService.setUpSimulationStep(this.interComponentCommunicationService.checklistLastActiveStep);
    this.router.navigate(['../wyniki'], { relativeTo: this.route, skipLocationChange: true });
  }
}
