import {AfterViewInit, Component, Inject, OnInit} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { TranslationLoaderService } from '@app/modules/shared-module/services/i18n/translation-loader.service';
import {CheckListData} from '../../model/CheckListData';
import { StepType } from '@app/modules/shared-module/model';
import { FormArray, FormGroup } from '@angular/forms';
import { ValidationLoader } from '@app/modules/shared-module/services/i18n/validations.loader';
import { FormlyFieldConfig, FormlyFormOptions } from '@ngx-formly/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ChecklistService } from '@app/modules/checklist/service/checklist.service';
import { LoaderComponent } from '@app/modules/shared-module/components/loader/loader.component';
import { ViewportScroller } from '@angular/common';
import {ChecklistFormConfigurationService} from '@app/modules/checklist/service/checklist-form-configuration.service';
import {catchError, map} from 'rxjs/operators';
import {EMPTY} from 'rxjs';
import {ChecklistGenerationResultDto} from '@app/modules/checklist/dto/ChecklistGenerationResultDto';
import {InterComponentCommunicationService} from '@app/modules/checklist/service/inter-component-communication.service';

@Component({
  selector: 'app-checklist-path',
  templateUrl: './checklist-path.component.html',
  styleUrls: ['./checklist-path.component.scss']
})
export class ChecklistPathComponent implements OnInit, AfterViewInit {
  methodUnavailable = false;
  steps: StepType[] = [];
  formSteps: FormlyFieldConfig[][] = [];
  form: FormArray;
  options: any;
  model: any;
  interComponentChecklistCommunicationService: InterComponentCommunicationService;
  viewportScrollAnchor: string;

  constructor(
    private translate: TranslateService,
    private translationLoader: TranslationLoaderService,
    private validationsLoader: ValidationLoader,
    private router: Router,
    private route: ActivatedRoute,
    private checklistService: ChecklistService,
    private viewportScroller: ViewportScroller,
    private checklistModelConfigurationService: ChecklistFormConfigurationService,
    private interComponentCommunicationService: InterComponentCommunicationService
  ) {
    this.translate.use('pl');
    this.translationLoader.loadTranslations();
    this.validationsLoader.init();
    this.steps = checklistModelConfigurationService.steps;
    this.interComponentChecklistCommunicationService = interComponentCommunicationService;
  }

  ngOnInit() {
    this.viewportScrollAnchor = this.interComponentCommunicationService.viewportScrollAnchor;
    if (this.interComponentChecklistCommunicationService.checkListData) {
      this.model = this.interComponentChecklistCommunicationService.checkListData;
    } else {
      this.model = {
        CheckListData: new CheckListData()
      };
    }
    const stepOneFields = this.checklistModelConfigurationService.formFieldStepOne();
    this.onInit(stepOneFields);
    this.validationsLoader.init();
    if (this.interComponentChecklistCommunicationService.checklistLastActiveStep === (this.steps.length - 1)) {
      this.interComponentChecklistCommunicationService.setIsChecklistResultView(true);
    }
  }

  ngAfterViewInit() {
    this.returnToTopChecklistPosition();
  }

  private onInit(stepOneFields: FormlyFieldConfig[]) {
    this.formSteps = [
      stepOneFields,
      this.checklistModelConfigurationService.formFieldStepTwo(),
      this.checklistModelConfigurationService.formFieldStepThree()
    ];

    this.form = new FormArray(this.formSteps.map(() => new FormGroup({})));
    this.options = this.steps.map(() => <FormlyFormOptions> {});
  }

  prevStep(step) {
    if (step - 1 >= 0) {
      this.interComponentChecklistCommunicationService.setUpSimulationStep(step - 1);
      this.returnToTopChecklistPosition();
    }
  }

  nextStep(step) {
    if (step + 1 < this.steps.length) {
      this.interComponentChecklistCommunicationService.setUpSimulationStep(step + 1);
      this.returnToTopChecklistPosition();
    }
  }

  submit(step) {
    if (this.form.valid) {
      this.interComponentChecklistCommunicationService.setChecklistData(this.model);
      LoaderComponent.show();
      this.checklistService.fetchDocuments(this.model.CheckListData).pipe(
        map((result: ChecklistGenerationResultDto) => {
          LoaderComponent.hide();
          this.methodUnavailable = false;
          this.checklistService.updateChecklistResult(result);
          this.interComponentChecklistCommunicationService.setUpSimulationStep(step + 1);
          this.interComponentChecklistCommunicationService.setIsChecklistResultView(true);
          return EMPTY;
      }),
        catchError(err => {
          console.log(err);
          this.methodUnavailable = true;
          LoaderComponent.hide();
          return err;
        })
    ).subscribe();
    }
  }

  private returnToTopChecklistPosition() {
    this.viewportScroller.scrollToAnchor(this.viewportScrollAnchor);
  }

  onAlertClosed(): void {
    this.methodUnavailable = false;
  }
}
