import { Component, OnChanges, Input, SimpleChange, OnInit } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { Subscription, Subject } from 'rxjs';
import { simulationParameters } from '../../../../../assets/parameters/systemParameters';

import { SimulationRequestDto } from '@app/modules/simulation/dto/SimulationRequestDto';
import { SimulationResponseDto } from '@app/modules/simulation/dto/SimulationResponseDto';

import { SimulationService } from '@app/modules/simulation/service/simulation.service';
import { SimulationResult } from '@app/modules/simulation/model/SimulationResult';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { CircleSliderOption } from '../../model';
import { CircularSlider } from './circularSlider';

// declare const CircularSlider;

@Component({
  selector: 'app-simulation-results-view',
  templateUrl: './simulation-results-view.component.html',
  styleUrls: ['./simulation-results-view.component.scss']
})
export class SimulationResultsViewComponent implements OnChanges, OnInit {
  @Input() simulation: SimulationResponseDto;
  @Input() simulationVariant: string;
  @Input() simulationParametres: SimulationRequestDto;

  isInstallmentView: boolean;
  isLoanView: boolean;

  sliderValueResultCircle: number;
  sliderValueLoanTermCircle: number;

  simulationSubscribtion: Subscription;
  apiMethodUnavailable = false;

  sliderChangeEvent = new Subject<string>();

  cirleSliderId = 'resultCircleRootSVG';
  termCirleSliderId = 'resultTermCircleRootSVG';

  resultCircleSlider: any;
  resultCircleSliderOptions: CircleSliderOption;

  loanTermCircleSlider: any;
  loanTermCircleSliderOptions: CircleSliderOption;

  minInstallment: number;

  constructor(private simulationService: SimulationService) { }

  ngOnInit() {
    this.checkViewType();
    this.initResultCircleSlider();
    this.initLoanTermCircleSlider();
    this.minInstallment = simulationParameters.minInstallment;
  }

  ngOnChanges(changes: { [property: string]: SimpleChange }) {
    const changeSimulation: SimpleChange = changes.simulation;
    const changeSimulationVariant: SimpleChange = changes.simulationVariant;
    const changeSimulationParametres: SimpleChange = changes.simulationParametres;
    this.trackSliderChagnes();

    if (this.resultCircleSlider) {
      this.updateResultViewAfterChange();
    }
  }

  private updateResultViewAfterChange() {
    if (this.isInstallmentView) {
      this.resultCircleSlider.stepNo = this.simulation.installmentMonthlyAmount - simulationParameters.minInstallment;
      this.loanTermCircleSlider.stepNo = this.simulation.numberOfInstallmentsInYears - simulationParameters.minLoanTermInYears;
    }

    if (this.isLoanView) {
      this.resultCircleSlider.stepNo = this.simulation.loanAmount - simulationParameters.mintotalLoanAmount;
      this.loanTermCircleSlider.stepNo = this.simulation.numberOfInstallmentsInYears - simulationParameters.minLoanTermInYears;
    }
  }

  private checkViewType() {
    this.isInstallmentView = (this.simulationVariant === 'INSTALMENT_VIEW') ? true : false;
    this.isLoanView = (this.simulationVariant === 'LOAN_VIEW') ? true : false;
  }

  private initResultCircleSlider() {
    if (this.isInstallmentView) {
      this.setUpInstalmentViewSlider();
    }

    if (this.isLoanView) {
      this.setUpLoanViewSlider();
    }
  }

  private setUpInstalmentViewSlider() {
    const min = simulationParameters.minInstallment;
    const max = simulationParameters.maxInstallment;

    this.resultCircleSliderOptions = new CircleSliderOption(
      'resultCircle', '#00965E', min, max, 1, 195,
      (val: number, eventType: string) => {
        this.installmentValueChange((val < 0 ? 1 : val), eventType);
      }
    );

    this.resultCircleSlider = new CircularSlider(this.cirleSliderId, this.resultCircleSliderOptions);
    this.resultCircleSlider.stepNo = this.simulation.installmentMonthlyAmount - simulationParameters.minInstallment;
  }

  private setUpLoanViewSlider() {
    const min = simulationParameters.mintotalLoanAmount;
    const max = simulationParameters.maxtotalLoanAmount;

    this.resultCircleSliderOptions = new CircleSliderOption(
      'resultCircle', '#00965E', min, max, 1, 195,
      (val: number, eventType: string) => {
        this.loanSliderValueChange((val < 0 ? 1 : val), eventType);
      }
    );

    this.resultCircleSlider = new CircularSlider(this.cirleSliderId, this.resultCircleSliderOptions);
    this.resultCircleSlider.stepNo = this.simulation.loanAmount - simulationParameters.mintotalLoanAmount;
  }

  private initLoanTermCircleSlider() {
    const min = simulationParameters.minLoanTermInYears;
    const max = simulationParameters.maxLoanTermInYears;

    this.loanTermCircleSliderOptions = new CircleSliderOption(
      'loanTermCircle', '#00965E', min, max, 1, 195,
      (val: number, eventType: string) => {
        this.numberOfInstallmentsInYearsChange((val < 0 ? 1 : val), eventType);
      }
    );

    this.loanTermCircleSlider = new CircularSlider(this.termCirleSliderId, this.loanTermCircleSliderOptions);
    this.loanTermCircleSlider.stepNo = this.simulation.numberOfInstallmentsInYears - simulationParameters.minLoanTermInYears;
  }

  private startTrackSlider(eventType: string) {
    if (eventType === 'click' || eventType === 'mouseup' || eventType === 'touchend') {
      this.sliderChangeEvent.next(eventType);
    }
  }

  private trackSliderChagnes() {
    this.sliderChangeEvent.next('');
    this.sliderChangeEvent
      .pipe(debounceTime(1000),
        distinctUntilChanged()
      )
      .subscribe(
        (data: string) => { this.startRecalculation(data); },
        (err) => console.error(err)
      );
  }

  private startRecalculation(newValue: string) {
    if (newValue) {
      this.recalculate(this.simulationParametres);
      this.sliderChangeEvent.next('');
    }
  }

  private installmentValueChange(val: number, eventType: string) {
    this.sliderValueResultCircle = val;
    if (this.simulation.installmentMonthlyAmount !== this.sliderValueResultCircle) {
      this.simulationParametres.installmentMonthlyAmount = this.sliderValueResultCircle;

      this.startTrackSlider(eventType);
    }
  }

  private loanSliderValueChange(val: number, eventType: string) {
    this.sliderValueResultCircle = val;
    if (this.simulation.loanAmount !== this.sliderValueResultCircle) {
      this.simulationParametres.totalLoanAmount = this.sliderValueResultCircle;
      this.startTrackSlider(eventType);
    }
  }

  private numberOfInstallmentsInYearsChange(val: number, eventType: string) {
    this.sliderValueLoanTermCircle = val;

    if (this.simulation.numberOfInstallmentsInYears !== this.sliderValueLoanTermCircle) {
      this.simulationParametres.simpleSimulationRequest.loanTermInYears = this.sliderValueLoanTermCircle;
      this.startTrackSlider(eventType);
    }
  }

  private recalculate(model: SimulationRequestDto) {
    this.simulationSubscribtion = this.simulationService.recaluculate(model)
      .subscribe(
        (result: SimulationResult) => {
          this.apiMethodUnavailable = true;
          this.simulationService.saveResults(result);
        },
        (error: HttpErrorResponse) => {
          console.log(error);
          this.apiMethodUnavailable = true;
        }
      );
  }
}
