import { Component, OnInit, AfterViewInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

import { SimulationService } from '@app/modules/simulation/service/simulation.service';
import { SimulationResponseDto } from '@app/modules/simulation/dto/SimulationResponseDto';
import { TranslationLoaderService } from '@app/modules/shared-module/services/i18n/translation-loader.service';
import { AppViewData } from '@app/modules/shared-module/model';
import { SimulationRequestDto } from '../../dto/SimulationRequestDto';
import { SpacePipe } from '@app/modules/shared-module/pipes/space.pipe';
import { FormGroup, FormArray } from '@angular/forms';
import { FormlyFieldConfig } from '@ngx-formly/core';
import { SimulationResultsCalc } from '../../model/SimulationResultsCalc';
import { InterComponentCommunicationService } from '../../service/inter-component-communication.service';
import { ViewportScroller } from '@angular/common';
import { Router, ActivatedRoute } from '@angular/router';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { SimulationFormModalComponent } from '../simulation-form-modal/simulation-form-modal.component';

@Component({
  selector: 'app-simulation-results',
  templateUrl: './simulation-results.component.html',
  styleUrls: ['./simulation-results.component.scss']
})
export class SimulationResultsComponent implements OnInit, AfterViewInit {
  viewportScrollAnchor: string;

  simulationResults: SimulationResponseDto[];
  simulationParametres;

  simulation: SimulationResponseDto;
  simulationVariant: string;
  simulationWarningInstallment: boolean;
  simulationWarningLoan: boolean;

  optimalOption: boolean;
  quickOption: boolean;
  safeOption: boolean;

  optimalVariantName = 'OPTIMAL';
  quickVariantName = 'QUICK';
  safeVariantName = 'SAFE';

  activeResult: string;
  activeResultArr = [];

  creditStandingPanelIsOpen = true;
  creditStandingView: AppViewData[];

  translations: any;

  parametersToChange: SimulationResultsCalc;
  form = new FormGroup({});
  model: any;
  fields: FormlyFieldConfig[];

  bsModalRef: BsModalRef;
  mobileForm: FormArray;
  mobileFormSteps: FormlyFieldConfig[][] = [];

  constructor(
    private translate: TranslateService,
    private translationLoader: TranslationLoaderService,
    private simulationService: SimulationService,
    private router: Router,
    private route: ActivatedRoute,
    private spacePipe: SpacePipe,
    private viewportScroller: ViewportScroller,
    private interComponentCommunicationService: InterComponentCommunicationService,
    private modalService: BsModalService
  ) {
    this.translate.use('pl');
    this.translationLoader.loadTranslations();

    this.translate.get('SimulatorResults').subscribe(translated => {
      this.translations = translated;
    });
  }

  ngOnInit() {
    this.simulationWarningInstallment = false;
    this.simulationWarningLoan = false;
    this.viewportScrollAnchor = this.interComponentCommunicationService.viewportScrollAnchor;

    this.simulationService.currentSimulationParameters.subscribe(data => {
      this.simulationParametres = data;

      this.parametersToChange =
        new SimulationResultsCalc(this.simulationService, this.simulationParametres, this.translate, this.spacePipe);

      if (this.simulationParametres.installmentMonthlyAmount) {
        this.fields =
          this.parametersToChange.formFieldInstallment();
      }
      if (this.simulationParametres.totalLoanAmount) {
        this.fields = this.parametersToChange.formFieldLoan();
      }

    });

    this.simulationService.currentSimulationResult.subscribe(data => {
      this.simulationResults = data.simulations;

      this.optimalOption = false;
      this.quickOption = false;
      this.safeOption = false;
      this.activeResultArr = [];

      this.setUpResults();
    });

  }

  ngAfterViewInit() {
    this.returnToTopPagePosition();
  }

  setUpResults() {
    if (this.simulationParametres.installmentMonthlyAmount) {
      this.simulationVariant = 'INSTALMENT_VIEW';
    } else {
      this.simulationVariant = 'LOAN_VIEW';
    }

    if (this.simulationResults.find(result => result.simulationVariant === this.optimalVariantName)) {
      this.optimalOption = true;
      this.simulationSetUp(this.optimalVariantName);
    }

    if (this.simulationResults.find(result => result.simulationVariant === this.quickVariantName)) {
      this.quickOption = true;
      this.simulationSetUp(this.quickVariantName);
    }

    if (this.simulationResults.find(result => result.simulationVariant === this.safeVariantName)) {
      this.safeOption = true;
      this.simulationSetUp(this.safeVariantName);
    }

    if (this.optimalOption) {
      this.activeResult = this.optimalVariantName;
    } else if (this.quickOption) {
      this.activeResult = this.quickVariantName;
    } else {
      this.activeResult = this.safeVariantName;
    }

    if (this.simulationResults.length > 0) {
      this.setUpView();
    }
  }

  simulationSetUp(variant: string) {
    this.activeResultArr.push(variant);
    this.simulation = this.simulationResults.find(result => result.simulationVariant === variant);
  }

  setUpView() {
    this.simulation = this.simulationResults.find(result => result.simulationVariant === this.activeResult);

    if (this.simulation.installmentMonthlyAmount < this.simulationParametres.installmentMonthlyAmount) {
      this.simulationWarningInstallment = true;
    } else {
      this.simulationWarningInstallment = false;
    }

    if (this.simulation.loanAmount < this.simulationParametres.totalLoanAmount) {
      this.simulationWarningLoan = true;
    } else {
      this.simulationWarningLoan = false;
    }


    const interestRate = this.simulation.interestRate * 100;
    this.creditStandingView = [
      {
        label: this.translations.panel.parameters.maxLoanCapability,
        data: `${this.spacePipe.transform(this.simulation.maxLoanCapability)}`,
        id: 'maxLoanCapability'
      },
      {
        label: this.translations.panel.parameters.maxPropertyValue,
        data: `${this.spacePipe.transform(this.simulation.propertyValue)}`,
        id: 'propertyValue'
      },
      {
        label: this.translations.panel.parameters.interestRate,
        data: `${Math.round((interestRate + Number.EPSILON) * 100) / 100}`,
        id: 'interestRate'
      }
    ];
  }

  showOptimal() {
    this.activeResult = this.optimalVariantName;
    this.setUpView();
  }

  showQuick() {
    this.activeResult = this.quickVariantName;
    this.setUpView();
  }

  showSafe() {
    this.activeResult = this.safeVariantName;
    this.setUpView();
  }

  goLeft() {
    const currentActiveOption = this.activeResultArr.indexOf(this.activeResult);

    if (currentActiveOption - 1 >= 0) {
      this.activeResult = this.activeResultArr[currentActiveOption - 1];
      this.setUpView();
    } else {
      this.activeResult = this.activeResultArr[this.activeResultArr.length - 1];
      this.setUpView();
    }
  }

  goRight() {
    const currentActiveOption = this.activeResultArr.indexOf(this.activeResult);

    if (currentActiveOption + 1 <= this.activeResultArr.length - 1) {
      this.activeResult = this.activeResultArr[currentActiveOption + 1];
      this.setUpView();
    } else {
      this.activeResult = this.activeResultArr[0];
      this.setUpView();
    }
  }

  private returnToTopPagePosition() {
    this.viewportScroller.scrollToAnchor(this.viewportScrollAnchor);
  }

  startNewSimulation() {
    this.interComponentCommunicationService.setUpSimulationStep(0);
    this.router.navigate(['../'], { relativeTo: this.route, skipLocationChange: true });
  }

  openModal() {
    let stepOneFields: FormlyFieldConfig[];
    if (this.simulationParametres.installmentMonthlyAmount) {
      stepOneFields =
        this.parametersToChange.mobileFormFieldInstallmentStepOne(false);
    }
    if (this.simulationParametres.totalLoanAmount) {
      stepOneFields = this.parametersToChange.mobileFormFieldLoanStepOne(false);
    }

    this.mobileFormSteps = [
      stepOneFields,
      this.parametersToChange.mobileFormFieldStepTwo()
    ];

    this.mobileForm = new FormArray(this.mobileFormSteps.map(() => new FormGroup({})));

    const initialState = {
      model: this.model,
      fields: this.fields,
      mobileForm: this.mobileForm,
      mobileFormSteps: this.mobileFormSteps
    };
    this.bsModalRef = this.modalService.show(
      SimulationFormModalComponent,
      Object.assign({ initialState }, { class: 'app-modal-full-screen' }));
  }
}
