import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {UserAuthService} from '@app/modules/shared-module/services/user-auth/user-auth.service';

@Component({
  selector: 'app-contact-base',
  templateUrl: './contact-base.component.html',
  styleUrls: ['./contact-base.component.scss']
})
export class ContactBaseComponent implements OnInit {

  constructor(private router: Router,
              public userService: UserAuthService) { }

  ngOnInit() {
    setTimeout(() => {
      this.router.navigate([{ outlets: { contactOutlet: ['contact'] } }], { skipLocationChange: true });
    }, 100);
  }

}
