import { FormlyFieldConfig } from '@ngx-formly/core';
import { TranslateService } from '@ngx-translate/core';
import { simulationParameters } from '../../../../assets/parameters/systemParameters';
import { SimulationRequestDto } from '../dto/SimulationRequestDto';
import { Subscription } from 'rxjs';
import { SimulationResult } from './SimulationResult';
import { LoaderComponent } from '@app/modules/shared-module/components/loader/loader.component';
import { HttpErrorResponse } from '@angular/common/http';
import { SimulationService } from '../service/simulation.service';
import { environment as env } from '@env/environment';
import { SpacePipe } from '@app/modules/shared-module/pipes';

export class SimulationResultsCalc {

  simulationSubscribtion: Subscription;
  apiMethodUnavailable = false;

  waitingForResults;
  calculateOnChange = true;

  constructor(
    private simulationService: SimulationService,
    private simulationParametres: SimulationRequestDto,
    private translations: TranslateService,
    private spacePipe: SpacePipe
  ) { }

  mobileFormFieldInstallmentStepOne(calculateOnChange: boolean): FormlyFieldConfig[] {
    this.calculateOnChange = calculateOnChange;
    return [
      {
        fieldGroup: [
          this.formFiledInstallmentMonthlyAmount(),
          this.formFiledLoanTermInYears(),
          this.formFiledExpectedContributionAmount(),
          this.formFiledFamilySize()
        ]
      }
    ];
  }

  mobileFormFieldLoanStepOne(calculateOnChange: boolean): FormlyFieldConfig[] {
    this.calculateOnChange = calculateOnChange;
    return [
      {
        fieldGroup: [
          this.formFiledTotalLoanAmount(),
          this.formFiledLoanTermInYears(),
          this.formFiledExpectedContributionAmount(),
          this.formFiledFamilySize()
        ],
      },
    ];
  }

  mobileFormFieldStepTwo(): FormlyFieldConfig[] {
    return [
      {
        fieldGroup: [
          this.formFiledTotalIncomeAmount(),
          this.formFiledTotalCostOfLiving(),
          this.formFiledTotalCostOfLiabilitiesLoan(),
          this.formFiledTotalCostOfLiabilitiesCreditCards()
        ],
      },
    ];
  }

  formFieldInstallment(): FormlyFieldConfig[] {
    return [
      {
        fieldGroup: [
          this.formFiledInstallmentMonthlyAmount(),
          this.formFiledLoanTermInYears(),
          this.formFiledExpectedContributionAmount(),
          this.formFiledFamilySize(),
          this.formFiledTotalIncomeAmount(),
          this.formFiledTotalCostOfLiving(),
          this.formFiledTotalCostOfLiabilitiesLoan(),
          this.formFiledTotalCostOfLiabilitiesCreditCards()
        ]
      }
    ];
  }

  formFieldLoan(): FormlyFieldConfig[] {
    return [
      {
        fieldGroup: [
          this.formFiledTotalLoanAmount(),
          this.formFiledLoanTermInYears(),
          this.formFiledExpectedContributionAmount(),
          this.formFiledFamilySize(),
          this.formFiledTotalIncomeAmount(),
          this.formFiledTotalCostOfLiving(),
          this.formFiledTotalCostOfLiabilitiesLoan(),
          this.formFiledTotalCostOfLiabilitiesCreditCards()
        ],
      },
    ];
  }

  private formFiledInstallmentMonthlyAmount(): FormlyFieldConfig {
    return {
      className: 'col-12 app-justify-content-center app-form-field',
      type: 'digit-input',
      wrappers: ['form-field-horizontal'],
      key: 'installmentMonthlyAmount',
      defaultValue: this.simulationParametres.installmentMonthlyAmount,
      templateOptions: {
        type: 'number',
        label: this.translations.instant('Simulator.fields.installmentMonthlyAmount.label'),
        placeholder: this.translations.instant('Simulator.fields.installmentMonthlyAmount.placeholder'),
        description:
          this.translations.instant(
            'Simulator.fields.installmentMonthlyAmount.description',
            {
              min: this.spacePipe.transform(simulationParameters.minInstallment),
              max: this.spacePipe.transform(simulationParameters.maxInstallment)
            }),
        attributes: {
          tooltip: this.translations.instant('Simulator.fields.installmentMonthlyAmount.tooltip')
        },
        required: true,
        min: simulationParameters.minInstallment,
        max: simulationParameters.maxInstallment,
        change: (field, $event) => { this.recalculateOnChange(field); },
      }
    };
  }

  private formFiledTotalLoanAmount(): FormlyFieldConfig {
    return {
      className: 'col-12 app-justify-content-center app-form-field',
      type: 'digit-input',
      wrappers: ['form-field-horizontal'],
      key: 'totalLoanAmount',
      defaultValue: this.simulationParametres.totalLoanAmount,
      templateOptions: {
        type: 'number',
        label: this.translations.instant('Simulator.fields.totalLoanAmount.label'),
        placeholder: this.translations.instant('Simulator.fields.totalLoanAmount.placeholder'),
        description:
          this.translations.instant(
            'Simulator.fields.totalLoanAmount.description',
            {
              min: this.spacePipe.transform(simulationParameters.mintotalLoanAmount),
              max: this.spacePipe.transform(simulationParameters.maxtotalLoanAmount)
            }),
        attributes: {
          tooltip: this.translations.instant('Simulator.fields.totalLoanAmount.tooltip')
        },
        required: true,
        min: simulationParameters.mintotalLoanAmount,
        max: simulationParameters.maxtotalLoanAmount,
        change: (field, $event) => { this.recalculateOnChange(field); }
      }
    };
  }

  private formFiledLoanTermInYears(): FormlyFieldConfig {
    return {
      className: 'col-12 app-justify-content-center app-form-field',
      type: 'digit-input',
      wrappers: ['form-field-horizontal'],
      key: 'loanTermInYears',
      defaultValue: this.simulationParametres.simpleSimulationRequest.loanTermInYears,
      templateOptions: {
        type: 'number',
        label: this.translations.instant('Simulator.fields.loanTermInYears.label'),
        placeholder: this.translations.instant('Simulator.fields.loanTermInYears.placeholder'),
        description:
          this.translations.instant(
            'Simulator.fields.loanTermInYears.description',
            { min: simulationParameters.minLoanTermInYears, max: simulationParameters.maxLoanTermInYears }),
        attributes: {
          tooltip: this.translations.instant('Simulator.fields.loanTermInYears.tooltip'),
          additionalLabel: this.translations.instant('Simulator.fields.loanTermInYears.additionalLabel'),
        },
        required: true,
        min: simulationParameters.minLoanTermInYears,
        max: simulationParameters.maxLoanTermInYears,
        change: (field, $event) => { this.recalculateOnChange(field); }
      }
    };
  }

  private formFiledExpectedContributionAmount(): FormlyFieldConfig {
    return {
      className: 'col-12 app-justify-content-center app-form-field',
      type: 'digit-input',
      wrappers: ['form-field-horizontal'],
      key: 'expectedContributionAmount',
      defaultValue: this.simulationParametres.simpleSimulationRequest.expectedContributionAmount,
      templateOptions: {
        type: 'number',
        label: this.translations.instant('Simulator.fields.expectedContributionAmount.label'),
        placeholder: this.translations.instant('Simulator.fields.expectedContributionAmount.placeholder'),
        description:
          this.translations.instant(
            'Simulator.fields.expectedContributionAmount.description',
            {
              min: this.spacePipe.transform(simulationParameters.minExpectedContributionAmount),
              max: this.spacePipe.transform(simulationParameters.maxExpectedContributionAmount)
            }),
        attributes: {
          tooltip: this.translations.instant('Simulator.fields.expectedContributionAmount.tooltip')
        },
        required: true,
        min: simulationParameters.minExpectedContributionAmount,
        max: simulationParameters.maxExpectedContributionAmount,
        change: (field, $event) => { this.recalculateOnChange(field); }
      }
    };
  }

  private formFiledFamilySize(): FormlyFieldConfig {
    return {
      className: 'col-12 app-justify-content-center app-form-field',
      type: 'digit-input',
      wrappers: ['form-field-horizontal'],
      key: 'familySize',
      defaultValue: this.simulationParametres.simpleSimulationRequest.familySize,
      templateOptions: {
        type: 'number',
        label: this.translations.instant('Simulator.fields.familySize.label'),
        placeholder: this.translations.instant('Simulator.fields.familySize.placeholder'),
        description:
          this.translations.instant(
            'Simulator.fields.familySize.description',
            { min: simulationParameters.minFamilySize, max: simulationParameters.maxFamilySize }),
        attributes: {
          tooltip: this.translations.instant('Simulator.fields.familySize.tooltip'),
          additionalLabel: this.translations.instant('Simulator.fields.familySize.additionalLabel'),
        },
        required: true,
        min: simulationParameters.minFamilySize,
        max: simulationParameters.maxFamilySize,
        change: (field, $event) => { this.recalculateOnChange(field); }
      }
    };
  }

  private formFiledTotalIncomeAmount(): FormlyFieldConfig {
    return {
      className: 'col-12 app-justify-content-center app-form-field',
      type: 'digit-input',
      wrappers: ['form-field-horizontal'],
      key: 'totalIncomeAmount',
      defaultValue: this.simulationParametres.simpleSimulationRequest.totalIncomeAmount,
      templateOptions: {
        type: 'number',
        label: this.translations.instant('Simulator.fields.totalIncomeAmount.label'),
        placeholder: this.translations.instant('Simulator.fields.totalIncomeAmount.placeholder'),
        description:
          this.translations.instant(
            'Simulator.fields.totalIncomeAmount.description',
            {
              min: this.spacePipe.transform(simulationParameters.minTotalIncomeAmount),
              max: this.spacePipe.transform(simulationParameters.maxTotalIncomeAmount)
            }),
        attributes: {
          tooltip: this.translations.instant('Simulator.fields.totalIncomeAmount.tooltip')
        },
        required: true,
        min: simulationParameters.minTotalIncomeAmount,
        max: simulationParameters.maxTotalIncomeAmount,
        change: (field, $event) => { this.recalculateOnChange(field); }
      }
    };
  }

  private formFiledTotalCostOfLiving(): FormlyFieldConfig {
    return {
      className: 'col-12 app-justify-content-center app-form-field',
      type: 'digit-input',
      wrappers: ['form-field-horizontal'],
      key: 'totalCostOfLiving',
      defaultValue: this.simulationParametres.simpleSimulationRequest.totalCostOfLiving,
      templateOptions: {
        type: 'number',
        label: this.translations.instant('Simulator.fields.totalCostOfLiving.label'),
        placeholder: this.translations.instant('Simulator.fields.totalCostOfLiving.placeholder'),
        description:
          this.translations.instant(
            'Simulator.fields.totalCostOfLiving.description',
            {
              min: this.spacePipe.transform(simulationParameters.minTotalCostOfLiving),
              max: this.spacePipe.transform(simulationParameters.maxTotalCostOfLiving)
            }),
        attributes: {
          tooltip: this.translations.instant('Simulator.fields.totalCostOfLiving.tooltip')
        },
        required: true,
        min: simulationParameters.minTotalCostOfLiving,
        max: simulationParameters.maxTotalCostOfLiving,
        change: (field, $event) => { this.recalculateOnChange(field); }
      }
    };
  }

  private formFiledTotalCostOfLiabilitiesLoan(): FormlyFieldConfig {
    return {
      className: 'col-12 app-justify-content-center app-form-field',
      type: 'digit-input',
      wrappers: ['form-field-horizontal'],
      key: 'totalCostOfLiabilitiesLoan',
      defaultValue: this.simulationParametres.simpleSimulationRequest.totalCostOfLiabilitiesLoan,
      templateOptions: {
        type: 'number',
        label: this.translations.instant('Simulator.fields.totalCostOfLiabilitiesLoan.label'),
        placeholder: this.translations.instant('Simulator.fields.totalCostOfLiabilitiesLoan.placeholder'),
        description:
          this.translations.instant(
            'Simulator.fields.totalCostOfLiabilitiesLoan.description',
            {
              min: this.spacePipe.transform(simulationParameters.minTotalCostOfLiabilitiesLoan),
              max: this.spacePipe.transform(simulationParameters.maxTotalCostOfLiabilitiesLoan)
            }),
        attributes: {
          tooltip: this.translations.instant('Simulator.fields.totalCostOfLiabilitiesLoan.tooltip')
        },
        required: true,
        min: simulationParameters.minTotalCostOfLiabilitiesLoan,
        max: simulationParameters.maxTotalCostOfLiabilitiesLoan,
        change: (field, $event) => { this.recalculateOnChange(field); }
      }
    };
  }

  private formFiledTotalCostOfLiabilitiesCreditCards(): FormlyFieldConfig {
    return {
      className: 'col-12 app-justify-content-center app-form-field',
      type: 'digit-input',
      wrappers: ['form-field-horizontal'],
      key: 'totalCostOfLiabilitiesCreditCards',
      defaultValue: this.simulationParametres.simpleSimulationRequest.totalCostOfLiabilitiesCreditCards,
      templateOptions: {
        type: 'number',
        label: this.translations.instant('Simulator.fields.totalCostOfLiabilitiesCreditCards.label'),
        placeholder: this.translations.instant('Simulator.fields.totalCostOfLiabilitiesCreditCards.placeholder'),
        description:
          this.translations.instant(
            'Simulator.fields.totalCostOfLiabilitiesCreditCards.description',
            {
              min: this.spacePipe.transform(simulationParameters.minTotalCostOfLiabilitiesCards),
              max: this.spacePipe.transform(simulationParameters.maxTotalCostOfLiabilitiesCards)
            }),
        attributes: {
          tooltip: this.translations.instant('Simulator.fields.totalCostOfLiabilitiesCreditCards.tooltip')
        },
        required: true,
        min: simulationParameters.minTotalCostOfLiabilitiesCards,
        max: simulationParameters.maxTotalCostOfLiabilitiesCards,
        change: (field, $event) => { this.recalculateOnChange(field); }
      }
    };
  }

  recalculateOnChange(field) {
    if (this.calculateOnChange === true) {
      clearTimeout(this.waitingForResults);
      if (field.formControl.status === 'VALID') {
        this.waitingForResults = setTimeout(() => this.recalculate(field.key, field.formControl.value), env.recalculationTimeoutInMillis);
      }
    }
  }

  recalculate(field: string, value: number) {
    const model = this.simulationParametres;
    if (field === 'installmentMonthlyAmount') {
      model.installmentMonthlyAmount = value;
    } else if (field === 'totalLoanAmount') {
      model.totalLoanAmount = value;
    } else {

      if (field in model.simpleSimulationRequest) {
        model.simpleSimulationRequest[field] = value;
      }
    }

    this.submit(model);
  }

  submit(model: SimulationRequestDto) {
    this.simulationSubscribtion = this.simulationService.recaluculate(model).subscribe(
      (result: SimulationResult) => {
        LoaderComponent.hide();
        this.apiMethodUnavailable = true;
        this.simulationService.saveResults(result);
      },
      (error: HttpErrorResponse) => {
        this.apiMethodUnavailable = true;
        LoaderComponent.hide();
      }
    );
    LoaderComponent.show().subscribe(
      () => {
        this.closeSimulationSubscriptionIfTooLongNotRespond();
      });
  }

  private closeSimulationSubscriptionIfTooLongNotRespond(): void {
    LoaderComponent.hide();
    this.apiMethodUnavailable = true;
    this.simulationSubscribtion.unsubscribe();
  }
}
