import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap';
import { FormlyFieldConfig } from '@ngx-formly/core';
import { FormArray } from '@angular/forms';
import { SimulationService } from '@app/modules/simulation/service/simulation.service';
import { LoaderComponent } from '@app/modules/shared-module/components/loader/loader.component';
import { SimulationResult } from '@app/modules/simulation/model/SimulationResult';

@Component({
  selector: 'app-simulation-form-modal',
  templateUrl: './simulation-form-modal.component.html',
  styleUrls: ['./simulation-form-modal.component.scss']
})
export class SimulationFormModalComponent implements OnInit {
  mobileForm: FormArray;
  model: any = {};
  fields: FormlyFieldConfig[];
  mobileFormSteps: FormlyFieldConfig[][] = [];
  activeStep = 0;

  constructor(
    public bsModalRef: BsModalRef,
    private simulationService: SimulationService
  ) { }

  ngOnInit() {

  }

  prevStep(step) {
    this.activeStep = step - 1;
    if (this.activeStep < 0) {
      this.activeStep = this.mobileFormSteps.length - 1;
    }
  }

  nextStep(step) {
    this.activeStep = step + 1;
    if (this.mobileFormSteps.length - 1 < this.activeStep) {
      this.activeStep = 0;
    }
  }

  setStep(step) {
    this.activeStep = step;
  }

  submit() {
    this.recalculate();
    this.bsModalRef.hide();
  }

  recalculate() {
    this.simulationService.getSimulationResults$(this.model).subscribe(
      (result: SimulationResult) => {
        LoaderComponent.hide();
        this.simulationService.saveResults(result);
      }
    );
  }
}
