import { Component } from '@angular/core';
import { UserAuthService } from '@app/modules/shared-module/services/user-auth/user-auth.service';
import { FormlyFieldConfig } from '@ngx-formly/core';
import { TranslateService } from '@ngx-translate/core';
import { TranslationLoaderService } from '@app/modules/shared-module/services/i18n/translation-loader.service';
import { LoaderComponent } from '@app/modules/shared-module/components/loader/loader.component';
import { ValidationLoader } from '@app/modules/shared-module/services/i18n/validations.loader';
import { Subscription } from 'rxjs/internal/Subscription';
import { FormGroup } from '@angular/forms';
import { HttpErrorResponse } from '@angular/common/http';
import {LoginRequest, UserContext} from '@app/modules/shared-module/model';

@Component({
  selector: 'app-login-home',
  templateUrl: './login-home.component.html'
})
export class LoginHomeComponent {

  translations: any;
  fields: FormlyFieldConfig[];
  form = new FormGroup({});
  model: any = {};
  methodUnavailable = false;
  errorMessage: string;
  loaderSubscription: Subscription;
  loginSubscription: Subscription;

  private readonly incorrectAuthenticationTranslationKey = 'Login.incorrectAuthentication';
  private readonly methodUnavailableTranslationKey = 'Global.MethodUnavailable';

  constructor(
    private translate: TranslateService,
    private translationLoader: TranslationLoaderService,
    private validationsLoader: ValidationLoader,
    public userService: UserAuthService,
  ) {
    this.translate.use('pl');
    this.translationLoader.loadTranslations();
    this.validationsLoader.init();
    this.fields = this.getFields();
    userService.setUserContext(UserContext.Broker);
    userService.setupUserSession();
  }

  getFields(): FormlyFieldConfig[] {
    const translate: TranslateService = this.translate;
    return [
      {
        fieldGroup: [
          {
            className: 'col-12 app-justify-content-center app-form-field',
            type: 'custom-input',
            wrappers: ['form-field-horizontal'],
            key: 'login',
            templateOptions: {
              type: 'text',
              placeholder: translate.instant('Login.loginForm.fields.login.label'),
              attributes: {},
              required: true,
            },

          },
          {
            className: 'col-12 app-justify-content-center app-form-field',
            type: 'custom-input',
            wrappers: ['form-field-horizontal'],
            key: 'pass',
            templateOptions: {
              type: 'password',
              placeholder: translate.instant('Login.loginForm.fields.password.label'),
              attributes: {},
              required: true
            }
          },
        ]
      }
    ];
  }

  submit() {
    if (this.form.valid) {
      delete this.model.selectAll;

      this.loginSubscription = this.userService.authenticateUser(this.model as LoginRequest)
        .subscribe(
          (authResposne: any) => {
            if (!authResposne.isAuthenticated) {
              this.methodUnavailable = true;
              this.errorMessage = this.incorrectAuthenticationTranslationKey;
            }
            LoaderComponent.hide();
            this.loaderSubscription.unsubscribe();
          },
          (error: HttpErrorResponse) => {
            this.methodUnavailable = true;
            this.errorMessage = this.incorrectAuthenticationTranslationKey;
            LoaderComponent.hide();
            this.loaderSubscription.unsubscribe();
          }
        );
      this.loaderSubscription = LoaderComponent.show().subscribe(
        () => {
          LoaderComponent.hide();
          this.methodUnavailable = true;
          this.errorMessage = this.methodUnavailableTranslationKey;
          this.loginSubscription.unsubscribe();
        });
    }
  }
  onAlertClosed(): void {
    this.methodUnavailable = false;
  }

}
