import { Injectable } from '@angular/core';
import { SimulationResult } from '@app/modules/simulation/model/SimulationResult';
import {
  SimpleSimulationRequestWithInput,
  SimulationRequestDto,
  SimulationResultsWithEmailBody
} from '../dto/SimulationRequestDto';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { environment as env } from '../../../../environments/environment';
import { Observable } from 'rxjs';
import { LoaderComponent } from '@app/modules/shared-module/components/loader/loader.component';
import { ContactDto } from '@app/modules/contact/dto/ContactRequestDto';

@Injectable({
  providedIn: 'root'
})
export class SimulationControlService {

  constructor(private httpClient: HttpClient) {
  }

  private prepareHeaders(): HttpHeaders {
    return new HttpHeaders().append('Content-Type', 'application/json');
  }


  getSimulationResultsByInstallment$(simpleSimulationRequest: SimulationRequestDto): Observable<SimulationResult> {
    const headers = this.prepareHeaders();
    LoaderComponent.show();
    if (env.simulatorUrl.isMock) {
      return this.httpClient.get<SimulationResult>(env.simulatorUrl.installment);
    } else {
      return this.httpClient.post<SimulationResult>(env.simulatorUrl.installment, simpleSimulationRequest, {headers});
    }
  }

  getSimulationResultsByLoan$(simpleSimulationRequest: SimulationRequestDto): Observable<SimulationResult> {
    const headers = this.prepareHeaders();
    if (env.simulatorUrl.isMock) {
      return this.httpClient.get<SimulationResult>(env.simulatorUrl.loan);
    } else {
      return this.httpClient.post<SimulationResult>(env.simulatorUrl.loan, simpleSimulationRequest, { headers });
    }
  }

  saveSimulation(result: SimpleSimulationRequestWithInput): Observable<any> {
    const headers = this.prepareHeaders();
    if (env.simulatorUrl.isMock) {
      return this.httpClient.get<any>(env.simulatorUrl.save);
    } else {
      return this.httpClient.post<any>(env.simulatorUrl.save, result, { headers });
    }
  }

  sendMailByInstallment$(simulationResultsWithEmailBody: SimulationResultsWithEmailBody): Observable<void> {
    const headers = this.prepareHeaders();
    if (env.checklist.isMock) {
      return this.httpClient.get<void>(env.simulation.emailUrl.installment);
    } else {
      return this.httpClient.post<void>(env.simulation.emailUrl.installment, simulationResultsWithEmailBody, { headers });
    }
  }

  sendMailByByLoan$(simulationResultsWithEmailBody: SimulationResultsWithEmailBody): Observable<void> {
    const headers = this.prepareHeaders();
    if (env.checklist.isMock) {
      return this.httpClient.get<void>(env.simulation.emailUrl.loan);
    } else {
      return this.httpClient.post<void>(env.simulation.emailUrl.loan, simulationResultsWithEmailBody, { headers });
    }
  }
}
