import { Component } from '@angular/core';
import { FieldType } from '@ngx-formly/core';

@Component({
  selector: 'app-checkbox-with-tooltip-onclick',
  templateUrl: './checkbox-with-tooltip-onclick.component.html',
  styleUrls: ['./checkbox-with-tooltip-onclick.component.scss']
})
export class CheckboxWithTooltipOnClickComponent extends FieldType {
  showTooltip = false;

  toggleTooltipOnClick() {
    this.showTooltip = !this.showTooltip;
  }

  hideTooltipOnClick() {
    this.showTooltip = false;
  }
}
