import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

import {
  ChecklistResultsComponent,
  ChecklistSendComponent,
   ChecklistSentComponent,
  ChecklistHomeComponent,
  ChecklistPathComponent
} from '@app/modules/checklist/components';

const routes: Routes = [
  {
    path: 'dokumenty',
    component: ChecklistHomeComponent,
    outlet: 'checklist'
  },
  {
    path: 'dokumenty/start',
    component: ChecklistPathComponent,
    outlet: 'checklist'
  },
  {
    path: 'dokumenty/wyniki',
    component: ChecklistResultsComponent,
    outlet: 'checklist'
  },
  {
    path: 'dokumenty/wyslij',
    component: ChecklistSendComponent,
    outlet: 'checklist'
  },
  {
    path: 'dokumenty/dziekujemy',
    component: ChecklistSentComponent,
    outlet: 'checklist'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ChecklistRoutingModule {}
