import { AfterViewInit, Component, OnDestroy, OnInit } from '@angular/core';

import { TranslateService } from '@ngx-translate/core';

import { FormArray, FormGroup } from '@angular/forms';
import { FormlyFieldConfig } from '@ngx-formly/core';

import { StepType } from '@app/modules/shared-module/model/comon';
import { Simulation } from '@app/modules/simulation/model/Simulation';

import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { SimulationService } from '@app/modules/simulation/service/simulation.service';
import { TranslationLoaderService } from '@app/modules/shared-module/services/i18n/translation-loader.service';
import { ValidationLoader } from '@app/modules/shared-module/services/i18n/validations.loader';
import { LoaderComponent } from '@app/modules/shared-module/components/loader/loader.component';
import { SimulationResult } from '@app/modules/simulation/model/SimulationResult';
import { HttpErrorResponse } from '@angular/common/http';
import { ViewportScroller } from '@angular/common';
import { InterComponentCommunicationService } from '../../service/inter-component-communication.service';
import { SpacePipe } from '@app/modules/shared-module/pipes';


@Component({
  selector: 'app-simulation-path',
  templateUrl: './simulation-path.component.html',
  styleUrls: ['./simulation-path.component.scss'],
})
export class SimulationPathComponent implements OnInit, OnDestroy, AfterViewInit {
  viewportScrollAnchor: string;

  routeSubscription: Subscription;
  simulationSubscribtion: Subscription;

  simulation = new Simulation(this.translate, this.spacePipe);
  model: any = {};
  form: FormArray;

  steps: StepType[] = [];
  formSteps: FormlyFieldConfig[][] = [];

  activeStep: number;
  isResultView = false;
  methodUnavailable = false;

  constructor(
    private translate: TranslateService,
    private translationLoader: TranslationLoaderService,
    private validationsLoader: ValidationLoader,
    private route: ActivatedRoute,
    private simulationService: SimulationService,
    private viewportScroller: ViewportScroller,
    private interComponentCommunicationService: InterComponentCommunicationService,
    private spacePipe: SpacePipe
  ) {
    this.translate.use('pl');
    this.translationLoader.loadTranslations();
    this.validationsLoader.init();
  }

  ngOnInit() {
    this.viewportScrollAnchor = this.interComponentCommunicationService.viewportScrollAnchor;
    this.routeSubscription = this.route.url.subscribe(path => {
      const simulationPath = path[1].path;

      let stepOneFields: FormlyFieldConfig[];
      switch (simulationPath) {
        case 'installment':
          stepOneFields = this.simulation.formFieldInstallmentStepOne();
          this.onInit(stepOneFields);
          break;
        case 'loan-amount':
          stepOneFields = this.simulation.formFieldLoanStepOne();
          this.onInit(stepOneFields);
          break;
        default:
          console.log('Niepoprawna ścieżka');
      }
    });
  }

  ngAfterViewInit() {
    this.returnToTopPagePosition();
  }

  ngOnDestroy() {
    this.routeSubscription.unsubscribe();
  }

  private onInit(stepOneFields: FormlyFieldConfig[]) {
    this.steps = [
      {
        label: this.translate.instant('Simulator.step1.label'),
        headerText: this.translate.instant('Simulator.step1.headerText'),
      },
      {
        label: this.translate.instant('Simulator.step2.label'),
        headerText: this.translate.instant('Simulator.step2.headerText'),
      },
      {
        label: this.translate.instant('Simulator.step3.label'),
        headerText: this.translate.instant('Simulator.step3.headerText'),
      }
    ];

    this.activeStep = this.interComponentCommunicationService.simulationLastActiveStep;
    if (this.activeStep === (this.steps.length - 1)) {
      this.isResultView = true;
    }

    this.formSteps = [
      stepOneFields,
      this.simulation.formFieldStepTwo()
    ];

    this.form = new FormArray(this.formSteps.map(() => new FormGroup({})));
  }

  prevStep(step) {
    this.activeStep = step - 1;
    this.returnToTopPagePosition();
  }

  nextStep(step) {
    this.activeStep = step + 1;
    this.returnToTopPagePosition();
  }

  submit(step) {
    if (this.form.valid) {

      this.simulationSubscribtion = this.simulationService.getSimulationResults$(this.model).subscribe(
        (result: SimulationResult) => {
          LoaderComponent.hide();
          this.methodUnavailable = false;
          this.simulationService.saveResults(result);
          this.activeStep = step + 1;
          this.isResultView = true;
          this.interComponentCommunicationService.simulationLastActiveStep = this.activeStep;
        },
        (error: HttpErrorResponse) => {
          console.log(error);
          this.methodUnavailable = true;
          LoaderComponent.hide();
        }
      );
      LoaderComponent.show().subscribe(
        () => {
          this.closeSimulationSubscriptionIfTooLongNotRespond();
        });
    }
  }

  private closeSimulationSubscriptionIfTooLongNotRespond(): void {
    LoaderComponent.hide();
    this.methodUnavailable = true;
    this.simulationSubscribtion.unsubscribe();
  }

  onAlertClosed(): void {
    this.methodUnavailable = false;
  }

  private returnToTopPagePosition() {
    this.viewportScroller.scrollToAnchor(this.viewportScrollAnchor);
  }
}
