import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { SimulationRoutingModule } from '@app/modules/simulation/simulation-routing.module';
import {
  SimulationBaseComponent,
  SimulationHomeComponent,
  SimulationPathComponent,
  SimulationResultsComponent,
  SimulationFormModalComponent,
  SimulationSendComponent,
  SimulationSentComponent
} from '@app/modules/simulation//components';

import { SharedModule } from '@app/modules/shared-module/shared.module';
import { DecimalPipe } from '@angular/common';
import { SpacePipe } from '../shared-module/pipes/space.pipe';

@NgModule({
  declarations: [
    SimulationHomeComponent,
    SimulationPathComponent,
    SimulationResultsComponent,
    SimulationSendComponent,
    SimulationSentComponent,
    SimulationBaseComponent,
    SimulationFormModalComponent
  ],
  entryComponents: [SimulationFormModalComponent],
  imports: [
    CommonModule,
    SimulationRoutingModule,
    SharedModule,
  ],
  providers: [DecimalPipe, SpacePipe]
})
export class SimulationModule { }
