import { Inject, Injectable} from '@angular/core';
import {Observable} from 'rxjs';
import {ContactControlService} from '@app/modules/contact/service/contact-control.service';
import {SESSION_STORAGE, StorageService} from 'ngx-webstorage-service';
import {ContactDto, EmailAddress, Permissions} from '@app/modules/contact/dto/ContactRequestDto';
import {LeadData} from '@app/modules/contact/model/contact';

@Injectable({
  providedIn: 'root'
})
export class ContactService {
  constructor(
    private contactControlService: ContactControlService,
    @Inject(SESSION_STORAGE) private storage: StorageService
  ) { }

  saveLead(leadData: LeadData): Observable<any> {
    const emailAddress: EmailAddress = {
      address: leadData.email
    };

    const permissions: Permissions = {
      marketingConsent: leadData.marketingConsent,
      digitalMarketingConsent: leadData.digitalMarketingConsent,
      offerPresentationConsent: leadData.offerPresentationConsent,
      digitalOfferPresentationConsent: leadData.digitalOfferPresentationConsent,
    };

    const contactDto: ContactDto = {
      lastName: leadData.lastName,
      firstName: leadData.firstName,
      phoneNumber: leadData.phoneNumber,
      emailAddress,
      permissions
    };

    const leadId: string = this.storage.get('leadId');
    return this.contactControlService.saveLead(contactDto, leadId);
  }

}
