import {Component, ElementRef, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {InterComponentCommunicationService} from '@app/modules/checklist/service/inter-component-communication.service';
import {UserAuthService} from '@app/modules/shared-module/services/user-auth/user-auth.service';

@Component({
  selector: 'app-checklist-base',
  templateUrl: './checklist-base.component.html',
  styleUrls: ['./checklist-base.component.scss']
})
export class ChecklistBaseComponent implements OnInit {
  constructor(
    private router: Router,
    private eltRef: ElementRef,
    private interComponentCommunicationService: InterComponentCommunicationService,
    public userService: UserAuthService
  ) {}

  ngOnInit() {
    sessionStorage.clear();
    setTimeout(() => {
      this.router.navigate([{outlets: {checklist: ['dokumenty']}}], {skipLocationChange: true});
    }, 200);

    const showSimulationBtn = this.eltRef.nativeElement.hasAttribute('simulationAnchor');

    if (showSimulationBtn) {
      const simulationAnchor = this.eltRef.nativeElement.getAttribute('simulationAnchor');
      this.interComponentCommunicationService.showSimulationBtn(simulationAnchor);
    }

    const hasViewportScrollAnchor = this.eltRef.nativeElement.hasAttribute('viewportScrollAnchor');

    if (hasViewportScrollAnchor) {
      const viewportScrollAnchor = this.eltRef.nativeElement.getAttribute('viewportScrollAnchor');
      this.interComponentCommunicationService.setViewportScrollAnchor(viewportScrollAnchor);
    }
  }
}
