import { FormlyFieldConfig } from '@ngx-formly/core';

export class FormOption {
  constructor(
    public label: string = '',
    public value: string = '',
  ) { }
}

export interface StepType {
  label: string;
  headerImgClass?: string;
  headerText?: string;
  fields?: FormlyFieldConfig[];
}

export class AppViewData {
  constructor(
    public label: string = '',
    public data: any = null,
    public id: string = ''
  ) { }
}

export class LoginRequest {
  login: string;
  pass: string;
}

export class LoginResponse {
  isAuthenticated: boolean;
  authenticationTime: string;
}

export enum UserContext {
  Customer,
  Broker
}

export class UserSession {
  userContext: UserContext = UserContext.Customer;
  loginDate: Date;
  authenticated = false;
  loginAttempt = 0;
}

export class CircleSliderOption {
  constructor(
    public container: string = '',
    public color: string = '',
    public min: number = 0,
    public max: number = 0,
    public step: number = 0,
    public radius: number = 0,
    public valueChange: (val: number, eventType: string) => void
  ) { }
}
