import {Inject, Injectable} from '@angular/core';
import {UserControlService} from '@app/modules/shared-module/services/user-auth/user-control.service';
import {Observable} from 'rxjs/internal/Observable';
import {LoginRequest, UserContext, UserSession} from '@app/modules/shared-module/model/comon';
import {LOCAL_STORAGE, StorageService} from 'ngx-webstorage-service';
import {environment as env} from '../../../../../environments/environment';
import {catchError, map} from 'rxjs/operators';
import {throwError} from 'rxjs/internal/observable/throwError';

@Injectable({
  providedIn: 'root'
})
export class UserAuthService {

  private userSession: UserSession = new UserSession();

  constructor(private userControlService: UserControlService,
              @Inject(LOCAL_STORAGE) private storage: StorageService) {
  }

  isBroker() {
    return this.userSession.userContext === UserContext.Broker;
  }

  setUserContext(userContext: UserContext) {
    this.userSession.userContext = userContext;
  }

  setupUserSession(): void {
    const savedLastLogin = this.getLoginDateFromLocalStorage();
    if ( this.isTimeOfLastLoginShorterThanSessionParam(new Date(savedLastLogin))) {
      this.loginCorrect();
    } else {
      this.logOut();
    }
  }

  private isTimeOfLastLoginShorterThanSessionParam(lastLogin: Date): boolean {
    return (lastLogin && this.getTimeDiffInMilisFromNow(lastLogin) < this.getTimeInMilis(env.timeInMinutesTillSessionExpired));
  }

  private getTimeInMilis(timeInMinutes: number): number {
    return timeInMinutes * 1000 * 60;
  }

  private getTimeDiffInMilisFromNow(diffDate: Date): number {
    return ( new Date().getTime() - new Date(diffDate).getTime());
  }

  private getTimeToCloseSessionInMilis(): number {
    const lastLogin = this.getLoginDateFromLocalStorage();
    if (this.isTimeOfLastLoginShorterThanSessionParam(lastLogin)) {
      return this.getTimeInMilis(env.timeInMinutesTillSessionExpired) - this.getTimeDiffInMilisFromNow(lastLogin);
    }
    return this.getTimeInMilis(env.timeInMinutesTillSessionExpired);
  }

  authenticateUser(loginRequest: LoginRequest): Observable<any> {
    return this.userControlService.authenticateUser(loginRequest).pipe(
      map(loginResponse => {
        if (loginResponse.isAuthenticated) {
          this.loginCorrect();
          return loginResponse;
        } else {
          this.loginIncorrect();
          return loginResponse;
        }
      }),
      catchError(err => {
        console.log(err);
        return throwError(err);
      })
    );
  }

  hasAccessToModule() {
    if (this.userSession && this.userSession.userContext === UserContext.Customer) {
      return true;
    }
    return this.userSession.authenticated;
  }

  private loginCorrect() {
    this.userSession.authenticated = true;
    this.userSession.loginDate = new Date();
    this.userSession.loginAttempt = 0;
    setTimeout(() => {
      this.logOut();
    }, this.getTimeToCloseSessionInMilis());
    if (!this.isTimeOfLastLoginShorterThanSessionParam(this.getLoginDateFromLocalStorage())) {
      this.setLoginDateToLocalStorage();
    }
  }

  private loginIncorrect() {
    this.userSession.loginAttempt++;
  }

  private logOut() {
    this.userSession.authenticated = false;
    this.userSession.loginAttempt = 0;
    this.userSession.loginDate = null;
  }

  private setLoginDateToLocalStorage() {
    this.storage.set('loginDate', new Date());
  }

  private getLoginDateFromLocalStorage(): Date {
    const savedLoginDate = this.storage.get('loginDate');
    if (savedLoginDate) {
      return new Date(savedLoginDate);
    }
    return null;
  }

}
