import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {environment as env} from '../../../../environments/environment';
import {Observable} from 'rxjs/internal/Observable';
import {ContactDto} from '@app/modules/contact/dto/ContactRequestDto';


@Injectable({
  providedIn: 'root'
})
export class ContactControlService {
  constructor(private httpClient: HttpClient) {
  }

  private prepareHeaders(): HttpHeaders {
    return new HttpHeaders().append('Content-Type', 'application/json');
  }

  saveLead(contact: ContactDto, id: string): Observable<any> {
    const headers = this.prepareHeaders();
    if (env.contact.isMock) {
      return this.httpClient.get<string>(env.contact.saveLead);
    } else {
      if (id) {
        return this.httpClient.put<string>(`${env.contact.saveLead}/${id}`, { contact }, {headers});
      } else {
        return this.httpClient.post<string>(env.contact.saveLead, { contact }, {headers});
      }
    }
  }
}
