import {Component, Inject} from '@angular/core';
import { FormlyFieldConfig } from '@ngx-formly/core';
import { FormControl, FormGroup } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { TranslationLoaderService } from '@app/modules/shared-module/services/i18n/translation-loader.service';
import { ValidationLoader } from '@app/modules/shared-module/services/i18n/validations.loader';
import {LoaderComponent} from '@app/modules/shared-module/components/loader/loader.component';
import {HttpErrorResponse} from '@angular/common/http';
import {Subscription} from 'rxjs/internal/Subscription';
import {ActivatedRoute, Router} from '@angular/router';
import {ViewportScroller} from '@angular/common';
import {ContactService} from '@app/modules/contact/service/contact.service';
import { StorageService, SESSION_STORAGE} from 'ngx-webstorage-service';
import {UserAuthService} from '@app/modules/shared-module/services/user-auth/user-auth.service';
import {environment as env} from '@env/environment';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent {
  translations: any;
  fields: FormlyFieldConfig[];
  form = new FormGroup({});
  model: any = {};
  contactSubscription: Subscription;
  methodUnavailable = false;
  rodoInformationURL: string;

  constructor(
    private translate: TranslateService,
    private translationLoader: TranslationLoaderService,
    private validationsLoader: ValidationLoader,
    private router: Router,
    private route: ActivatedRoute,
    private viewportScroller: ViewportScroller,
    private contactService: ContactService,
    @Inject(SESSION_STORAGE) private storage: StorageService,
    public userService: UserAuthService
  ) {
    this.translate.use('pl');
    this.translationLoader.loadTranslations();
    this.validationsLoader.init();

    this.translate.get('SimulatorResults').subscribe(translated => {
      this.translations = translated;
    });

    this.fields = this.getFields();
    this.rodoInformationURL = env.rodoInformationURL;
  }

  getFields(): FormlyFieldConfig[] {
    const translate: TranslateService = this.translate;
    return [
      {
        fieldGroup: [
          {
            className: 'col-12 app-justify-content-center app-form-field',
            type: 'custom-input',
            wrappers: ['form-field-horizontal'],
            key: 'firstName',
            templateOptions: {
              type: 'text',
              placeholder: translate.instant('ContactForm.fields.firstName.label'),
              attributes: {},
              required: true,
            },
            validators: {
              validation: ['firstName']
            },
          },
          {
            className: 'col-12 app-justify-content-center app-form-field',
            type: 'custom-input',
            wrappers: ['form-field-horizontal'],
            key: 'lastName',
            templateOptions: {
              type: 'text',
              placeholder: translate.instant('ContactForm.fields.lastName.label'),
              attributes: {},
              required: true
            },
            validators: {
              validation: ['lastName']
            },
          },
          {
            className: 'col-12 app-justify-content-center app-form-field',
            type: 'custom-input',
            wrappers: ['form-field-horizontal'],
            key: 'email',
            templateOptions: {
              type: 'email',
              placeholder: translate.instant('ContactForm.fields.email.label'),
              attributes: {},
              required: true,
            },
            validators: {
              validation: ['email'],
            },
          },
          {
            className: 'col-12 app-justify-content-center app-form-field',
            type: 'custom-input',
            wrappers: ['form-field-horizontal'],
            key: 'phoneNumber',
            templateOptions: {
              type: 'tel',
              placeholder: translate.instant('ContactForm.fields.phoneNumber.label'),
              attributes: {},
              required: true,
            },
            validators: {
              validation: ['phone'],
            },
          }
        ]
      },
      {
        className: 'app-checkbox-list',
        fieldGroup: [
          {
            className: 'app-checkbox',
            key: 'selectAll',
            type: 'checkbox-with-tooltip-onclick',
            defaultValue: false,
            templateOptions: {
              label: translate.instant('ContactForm.consents.selectAll'),
              description: '',
              attributes: {
                tooltip: ''
              },
            },
          },
          {
            className: 'app-checkbox',
            key: 'marketingConsent',
            type: 'checkbox-with-tooltip-onclick',
            defaultValue: false,
            templateOptions: {
              label: translate.instant('ContactForm.consents.marketingConsent.labelShort'),
              description: '',
              attributes: {
                tooltip: translate.instant('ContactForm.consents.marketingConsent.label')
              },
            },
            expressionProperties: {
              'model.marketingConsent': 'model.selectAll'
            }
          },
          {
            className: 'app-checkbox',
            key: 'digitalMarketingConsent',
            type: 'checkbox-with-tooltip-onclick',
            defaultValue: false,
            templateOptions: {
              label: translate.instant('ContactForm.consents.digitalMarketingConsent.labelShort'),
              description: '',
              attributes: {
                tooltip: translate.instant('ContactForm.consents.digitalMarketingConsent.label')
              },
            },
            expressionProperties: {
              'model.digitalMarketingConsent': 'model.selectAll'
            }
          },
          {
            className: 'app-checkbox',
            key: 'offerPresentationConsent',
            type: 'checkbox-with-tooltip-onclick',
            defaultValue: false,
            templateOptions: {
              label: translate.instant('ContactForm.consents.offerPresentationConsent.labelShort'),
              description: '',
              attributes: {
                tooltip: translate.instant('ContactForm.consents.offerPresentationConsent.label')
              },
            },
            expressionProperties: {
              'model.offerPresentationConsent': 'model.selectAll'
            }
          },
          {
            className: 'app-checkbox',
            key: 'digitalOfferPresentationConsent',
            type: 'checkbox-with-tooltip-onclick',
            defaultValue: false,
            templateOptions: {
              label: translate.instant('ContactForm.consents.digitalOfferPresentationConsent.labelShort'),
              description: '',
              attributes: {
                tooltip: translate.instant('ContactForm.consents.digitalOfferPresentationConsent.label')
              },
            },
            expressionProperties: {
              'model.digitalOfferPresentationConsent': 'model.selectAll'
            }
          }
        ]
      }
    ];
  }

  submit() {
    if (this.form.valid) {
      delete this.model.selectAll;

      this.contactSubscription = this.contactService.saveLead(this.model)
        .subscribe(
          (saveLeadResult: any) => {
            this.storage.set('leadId', saveLeadResult.id);
            this.router.navigate(['sendContactConfirmation'], {relativeTo: this.route, skipLocationChange: true });
            LoaderComponent.hide();
          },
          (error: HttpErrorResponse) => {
            console.log(error);
            this.methodUnavailable = true;
            LoaderComponent.hide();
          }
        );
      LoaderComponent.show().subscribe(
        () => {
          LoaderComponent.hide();
          this.methodUnavailable = true;
          this.contactSubscription.unsubscribe();
        });
    }
  }
  onAlertClosed(): void {
    this.methodUnavailable = false;
  }
}
