import {AfterViewInit, Component, Inject, OnInit} from '@angular/core';
import {TranslateService} from '@ngx-translate/core';
import {TranslationLoaderService} from '@app/modules/shared-module/services/i18n/translation-loader.service';
import {ValidationLoader} from '@app/modules/shared-module/services/i18n/validations.loader';
import {ChecklistService} from '@app/modules/checklist/service/checklist.service';
import {ViewportScroller} from '@angular/common';
import {StepType} from '@app/modules/shared-module/model';
import {ChecklistFormConfigurationService} from '@app/modules/checklist/service/checklist-form-configuration.service';
import {ActivatedRoute, Router} from '@angular/router';
import {ChecklistGenerationResultDto} from '@app/modules/checklist/dto/ChecklistGenerationResultDto';
import {InterComponentCommunicationService} from '@app/modules/checklist/service/inter-component-communication.service';

@Component({
  selector: 'app-checklist-results',
  templateUrl: './checklist-results.component.html',
  styleUrls: ['./checklist-results.component.scss']
})
export class ChecklistResultsComponent implements OnInit, AfterViewInit {
  private translations: any;
  private viewportScrollAnchor: string;
  steps: StepType[] = [];
  checklistResult: ChecklistGenerationResultDto;
  interComponentChecklistCommunicationService: InterComponentCommunicationService;
  sections = [
    {
      code: 'applicants',
      visible: true,
      subsections: null
    },
    {
      code: 'purposes',
      visible: true,
      subsections: null
    }
  ];

  constructor(
    private translate: TranslateService,
    private translationLoader: TranslationLoaderService,
    private validationsLoader: ValidationLoader,
    private checklistService: ChecklistService,
    private viewportScroller: ViewportScroller,
    private checklistModelConfigurationService: ChecklistFormConfigurationService,
    private router: Router,
    private route: ActivatedRoute,
    private interComponentCommunicationService: InterComponentCommunicationService
  ) {
    this.translate.use('pl');
    this.translationLoader.loadTranslations();
    this.validationsLoader.init();

    this.translate.get('SimulatorResults').subscribe(
      translated => {
        this.translations = translated;
      });
    this.steps = this.checklistModelConfigurationService.steps;
    this.interComponentChecklistCommunicationService = interComponentCommunicationService;
  }

  ngAfterViewInit() {
    this.returnToTopChecklistPosition();
  }

  ngOnInit(): void {
    this.viewportScrollAnchor = this.interComponentCommunicationService.viewportScrollAnchor;
    const result = this.checklistService.getCheckListResults();
    this.checklistResult = result;
    for (let i = 0; i < result.documents.sections.length; i++) {
      this.sections[i].subsections = result.documents.sections[i].entries;
      this.sections[i].subsections.forEach(subsection => {
        subsection.visible = false;
      });
    }
  }

  toggle(sectionIndex, subSectionIndex) {
    this.sections[sectionIndex].subsections[subSectionIndex] = {
      ... this.sections[sectionIndex].subsections[subSectionIndex], visible: ! this.sections[sectionIndex].subsections[subSectionIndex].visible
    };
    this.viewportScroller.scrollToAnchor(`subsection-${subSectionIndex}`);
  }

  private returnToTopChecklistPosition() {
    this.viewportScroller.scrollToAnchor(this.viewportScrollAnchor);
  }

  goBack() {
    this.returnToTopChecklistPosition();
    this.interComponentCommunicationService.setIsChecklistResultView(false);
    this.interComponentCommunicationService.setUpSimulationStep(0);
    this.router.navigate(['../start'], { relativeTo: this.route, skipLocationChange: true }).catch(error => {
      console.log(error);
    });
  }
}
