import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/internal/Observable';
import {environment as env} from '../../../../../environments/environment';
import {LoginRequest, LoginResponse} from '@app/modules/shared-module/model';

@Injectable({
  providedIn: 'root'
})
export class UserControlService {

  constructor(private httpClient: HttpClient) {
  }

  private prepareHeaders(): HttpHeaders {
    return new HttpHeaders().append('Content-Type', 'application/json');
  }

  authenticateUser(loginRequest: LoginRequest): Observable<any> {
    const headers = this.prepareHeaders();
    if (env.user.isMock) {
      return new Observable((observer) => {
        setTimeout(() => {
          observer.next({ isAuthenticated: true,
          authenticationTime: new Date().toString()});
        }, 2000);
      });
    } else {
        return this.httpClient.post<LoginResponse>(env.user.authenticationUrl, loginRequest, {headers});
    }
  }
}
