import { Component, OnInit, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { InterComponentCommunicationService } from '../../service/inter-component-communication.service';
import {UserAuthService} from '@app/modules/shared-module/services/user-auth/user-auth.service';

@Component({
  selector: 'app-simulation-base',
  templateUrl: './simulation-base.component.html',
  styleUrls: ['./simulation-base.component.scss']
})
export class SimulationBaseComponent implements OnInit {
  constructor(
    private router: Router,
    private eltRef: ElementRef,
    private interComponentCommunicationService: InterComponentCommunicationService,
    public userService: UserAuthService
  ) {}

  ngOnInit() {
    sessionStorage.clear();
    this.router.navigate([{ outlets: { simulationOutlet: ['simulation'] } }], { skipLocationChange: true });

    const showChecklistBtn = this.eltRef.nativeElement.hasAttribute('checklistAnchor');

    if (showChecklistBtn) {
      const checklistAnchor = this.eltRef.nativeElement.getAttribute('checklistAnchor');
      this.interComponentCommunicationService.showChecklistBtn(checklistAnchor);
    }

    const hasViewportScrollAnchor = this.eltRef.nativeElement.hasAttribute('viewportScrollAnchor');

    if (hasViewportScrollAnchor) {
      const viewportScrollAnchor = this.eltRef.nativeElement.getAttribute('viewportScrollAnchor');
      this.interComponentCommunicationService.setViewportScrollAnchor(viewportScrollAnchor);
    }
  }
}
